<template>
    <div class="">
        <div class="mt-4 mb-2">
            <label class="page-header">TERMS AND CONDITIONS</label>
        </div>

        <div class="row">
            <div class="col-12">
                <div class="table-responsive">
                    <table v-for="(item, index) in data" :key="index" class="table">
                        <thead>
                            <tr>
                                <th @click="doSortTable('name')">CATEGORY</th>
                                <th @click="doSortTable('name')">{{ item.title_1 }} </th>
                                <th @click="doSortTable('name')">{{ item.title_2 }}</th>
                                <th style="width: 24%;">ACTION</th>
                            </tr>
                        </thead>
                        <tbody >
                            <tr >
                                <td class="align-baseline">
                                    <span class="d-flex text-left">{{ item.category }}</span>
                                </td>
                                <td>
                                    <span class="d-flex text-justify">{{ item.description_1}}</span>
                                </td>
                                <td>
                                    <span class="d-flex text-justify">{{ item.description_2 }}</span>
                                </td>
                                <td class="align-baseline">
                                    <router-link :to="{ name: 'edit_terms_and_conditions', params: {id: item.id} }" class="btn btn-main inverse small mt-1 mb-1">EDIT</router-link>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>

            
            </div>
        </div>

        <div class="mt-4">
            <label class="page-header">PROOF OF HUSTLERS</label>
        </div>

        <div class="row">
            <div class="col-12">
                <div class="table-responsive">
                    <table v-for="(item, index) in data_proof_of_hustler" :key="index" class="table">
                        <thead>
                            <tr>
                             
                                <th @click="doSortTable('name')">{{ item.title_1 }} </th>
                            
                                <th style="width: 24%;">ACTION</th>
                            </tr>
                        </thead>
                        <tbody >
                            <tr >
                    
                                <td>
                                    <span class="d-flex text-justify">{{ item.description_1}}</span>
                                </td>
                              
                                <td class="align-baseline">
                                    <router-link :to="{ name: 'edit_proof_of_hustler', params: {id: item.id} }" class="btn btn-main inverse small mt-1 mb-1">EDIT</router-link>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>

      
            </div>
        </div>
       
    </div>
</template>

<script>
    import arrow from '@/assets/images/ic_arrow_square_hover.svg'

import Paginate from 'vuejs-paginate'
import { mapState } from 'vuex'

    export default {
        name: 'location-list',
        components: {
            'paginate': Paginate,

        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        data (){
            return {
                profile: {},
                keyword: '',
              
                is_loading: false,
                data: [],
                data_proof_of_hustler: [],
   
                delete_confirmation: false,
     
                arrow: arrow,
            }
        },
        mounted(){
            this.getAuthUser()
            this.getListTermnCondition(true)
            this.getListProofHustler(true)
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                        }, response => {})
                    } else{
                        this.profile = this.user
                    }
                }
            },
        
            getListTermnCondition(loading){
                this.is_loading = loading

                this.$http.get(process.env.API_URL + 'term-and-condition').then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.data = response.data.data
                 
                    }
                }, response => {})
            },
            getListProofHustler(loading, ){
                this.is_loading = loading

                this.$http.get(process.env.API_URL + 'term-and-condition/membership').then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.data_proof_of_hustler = response.data.data
   
                    }
                }, response => {})
            },
 
        }
    }
</script>

<style lang="css" scoped>

</style>



