<template lang="html">
    <div class="">
        <div class="mb-4">
            <label class="page-header">USER</label>
            <label class="breadcrumb">User List/Edit User</label>
        </div>
        <label class="page-title">EDIT USER</label>
        <div class="row">
            <div class="col-12 col-sm-11 col-md-10 col-lg-8">
                <div class="" v-if="is_load_data">
                    <i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i>
                </div>
                <form v-else class="mt-3" @submit.prevent="doUpdate()">
                    <!-- <div class="form-group mb-4" v-if="profile.id !== data.id && profile.isAdmin && !data.isMember">
                        <label class="mb-2">USER TYPE</label>
                        <div class="">
                            <div class="form-check form-check-inline mb-1">
                                <input class="form-check-input" type="radio" v-model="form.role" id="user_type-admin" value="Admin">
                                <label class="form-check-label" for="user_type-admin">
                                    Admin
                                </label>
                            </div>
                            <div class="form-check form-check-inline mb-1">
                                <input class="form-check-input" type="radio" v-model="form.role" id="user_type-manager" value="Manager">
                                <label class="form-check-label" for="user_type-manager">
                                    Manager
                                </label>
                            </div>
                            <div class="form-check form-check-inline">
                                <input class="form-check-input" type="radio" v-model="form.role" id="user_type-operator" value="Operator">
                                <label class="form-check-label" for="user_type-operator">
                                    Operator
                                </label>
                            </div>
                        </div>
                    </div> -->

                    <div class="row">
                        <div class="col-12 col-md-6">
                            <div class="form-group mb-4">
                                <label>FIRST NAME <span class="red">*</span> </label>
                                <input type="text" class="form-control" v-model="form.firstName" required>
                            </div>
                        </div>
                        <div class="col-12 col-md-6">
                            <div class="form-group mb-4">
                                <label>LAST NAME  <span class="red">*</span> </label>
                                <input type="text" class="form-control" v-model="form.lastName" required>
                            </div>
                        </div>
                    </div>
                    <div class="form-group mb-4">
                        <label>EMAIL  <span class="red">*</span> </label>
                        <input type="email" class="form-control" v-model="form.email" required>
                    </div>

                    <div class="mb-5" v-if="form.role == 'Member'">
                        <div class="form-group mb-4">
                            <label>MOBILE NUMBER  <span class="red">*</span> </label>
                            <input type="text" class="form-control" v-model="form.phone" required>
                        </div>
                        <div class="row">
                            <div class="col-12 col-md-6">
                                <div class="form-group mb-4">
                                    <label>EMERGENCY CONTACT NAME <span class="red">*</span> </label>
                                    <input type="text" class="form-control" v-model="form.emergencyName" required>
                                </div>
                            </div>
                            <div class="col-12 col-md-6">
                                <div class="form-group mb-4">
                                    <label>EMERGENCY CONTACT PHONE NO.  <span class="red">*</span> </label>
                                    <input type="text" class="form-control" v-model="form.emergencyPhone" required>
                                </div>
                            </div>
                        </div>
                        <div class="form-group mb-4">
                            <label>HOME ADDRESS </label>
                            <input type="text" class="form-control" v-model="form.address">
                        </div>
                        <div class="row">
                            <div class="col-12 col-md-5">
                                <div class="form-group mb-5">
                                    <label class="mb-2">DATE OF BIRTH <span class="red">*</span></label>
                                    <div class="input-group front back">
                                        <span class="front icon"> <i class="fa fa-calendar" aria-hidden="true"></i> </span>
                                        <span class="back icon" v-if="form.dateOfBirth !== ''">
                                            <a href="#deleteDate" @click.prevent="form.dateOfBirth = ''">
                                                <i class="fa fa-times" aria-hidden="true"></i>
                                            </a>
                                        </span>
                                    </div>
                                    <datetime v-model="form.dateOfBirth" format="dd MMMM yyyy" input-class="form-control datetime front" class="theme light" :auto="true" :week-start="7" required></datetime>
                                </div>
                            </div>
                        </div>
                        <div class="form-group mb-4">
                            <label class="mb-2">GENDER <span class="red">*</span></label>
                            <div class="">
                                <div class="radio-group row">
                                    <div class="col">
                                        <input type="radio" id="male" value="Male" v-model="form.gender">
                                        <label for="male">MALE</label>
                                    </div>
                                    <div class="col">
                                        <input type="radio" id="female" value="Female" v-model="form.gender">
                                        <label for="female">FEMALE</label>
                                    </div>
                                    <div class="col"></div>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="mt-5">
                        <button type="submit" class="btn btn-main small mr-2" :disabled="is_loading">
                            <span v-if="!is_loading">SAVE</span>
                            <span class="loading" v-else>
                                <i class="fa fa-spinner fa-pulse fa-fw"></i>
                            </span>
                        </button>
                        <router-link :to="{ name: 'detail_user', params: {id: id} }" class="btn btn-main inverse small">CANCEL</router-link>
                    </div>

                    <div class="loading">

                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import { Datetime } from 'vue-datetime'

    export default {
        name: '',
        props: ['id'],
        components: {
            'datetime': Datetime
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        data (){
            return {
                profile: {},

                is_member: false,
                is_can_edit: false,
                is_load_data: false,
                is_loading: false,

                data: {},
                form: {
                    role: '',
                    firstName: '',
                    lastName: '',
                    email: '',
                    phone: '',
                    emergencyName: '',
                    emergencyPhone: '',
                    address: '',
                    gender: 'Male',
                    dateOfBirth: ''
                }
            }
        },
        mounted(){
            this.getAuthUser()
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                            this.getRoleAuthUser()
                            this.getDetailUser()
                        }, response => {})
                    } else{
                        this.profile = this.user
                        this.getRoleAuthUser()
                        this.getDetailUser()
                    }
                }
            },
            getRoleAuthUser(){
                if(this.profile.isAdmin){
                    this.form.role = 'Admin'
                } else if(this.profile.isManager){
                    this.form.role = 'Manager'
                } else if(this.profile.isOperator){
                    this.form.role = 'Operator'
                }
            },
            getDetailUser(){
                this.is_load_data = true
                this.$http.get(process.env.API_URL + 'user/' + this.id).then(response => {
                    this.is_load_data = false
                    var status = response.data.status
                    if(status){
                        this.data = response.data.data
                        this.is_member = this.data.isMember

                        this.form.firstName = this.data.firstName
                        this.form.lastName = this.data.lastName
                        this.form.email = this.data.email
                        if(this.is_member){
                            this.form.role = 'Member'
                            this.form.phone = this.data.member.phone
                            this.form.emergencyName = this.data.member.emergencyName
                            this.form.emergencyPhone = this.data.member.emergencyPhone
                            this.form.address = this.data.member.address
                            this.form.dateOfBirth = this.data.member.dateOfBirth + 'T00:00:00.000Z'
                            this.form.gender = this.data.member.gender

                        } else {
                            if(this.data.isAdmin){
                                this.form.role = 'Admin'
                            } else if(this.data.isManager){
                                this.form.role = 'Manager'
                            } else if(this.data.isOperator){
                                this.form.role = 'Operator'
                            }
                        }

                        if(this.profile.isOperator){
                            if(this.data.isOperator || this.data.isMember){
                                this.is_can_edit = true
                            } else {
                                this.is_can_edit = false
                            }
                        } else if(this.profile.isManager){
                            if(this.data.isManager || this.data.isOperator || this.data.isMember){
                                this.is_can_edit = true
                            } else {
                                this.is_can_edit = false
                            }
                        } else {
                            this.is_can_edit = true
                        }

                        if(!this.is_can_edit){
                            this.$toastr('error', "You don't have authorization to edit this user!")
                            this.$router.push({ name: 'users', params: {} })
                        }
                    }
                }, response => {})
            },
            doUpdate(){
                this.is_loading = true
                if(this.form.role !== 'Member'){
                    this.form.gender = ''
                }

                var moment = require('moment')
                this.form.dateOfBirth = moment(this.form.dateOfBirth).format('YYYY-MM-DD')

                this.$http.post(process.env.API_URL + 'user/update/' + this.id, this.form).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        if(this.profile.id == this.data.id){
                            this.$parent.profile = response.data.data
                        }
                        this.$router.push({ name: 'detail_user', params: {id: this.id} })
                        this.$toastr('success', response.data.message)
                    }
                }, response => {})
            }
        }
    }
</script>

<style lang="css" scoped>
    .radio-group input[type=radio] {
        position: absolute;
        visibility: hidden;
        display: none;
    }

    .radio-group label {
        color: #ef3b29;
        display: inline-block;
        cursor: pointer;
        font-weight: bold;
        padding: 9px 20px;
        width: 100%;
        border: solid 1px #ef3b29;
    }
    .radio-group label::before{
        content: none;
    }
    .radio-group label::after{
        content: none;
    }

    .radio-group input[type=radio]:checked + label {
        color: #fff;
        background: #ef3b29;
        border: solid 1px #ef3b29;
    }

    .radio-group label + input[type=radio] + label{
        border: solid 1px #ef3b29;
    }

    .radio-group {
        overflow: hidden;
    }
</style>



