<template>
  <div class="">
    <div class="mb-4">
      <label class="page-header">CUSTOMER FEEDBACK</label>
      <label class="breadcrumb"
        >Customer Feedback List/Edit Customer Feedback</label
      >
    </div>
    <label class="page-title">EDIT CUSTOMER FEEDBACK</label>
    <div class="row">
      <div class="col-12 col-sm-11 col-md-10 col-lg-8">
        <div class="" v-if="is_load_data">
          <i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i>
        </div>
        <form v-else class="mt-3" @submit.prevent="doUpdate()">
          <div class="row">
            <div class="col-12 col-md-6">
              <div class="form-group ">
                <label>ISSHOWED <span class="red">*</span> </label>
                <div class="mt-2">
                  <div class="form-check form-check-inline mb-1">
                    <input
                      class="form-check-input"
                      type="radio"
                      v-model="form.isShowed"
                      id="discount_type-rp"
                      value="0"
                    />
                    <label class="form-check-label" for="discount_type-rp">
                      False
                    </label>
                  </div>
                  <div class="form-check form-check-inline mb-1">
                    <input
                      class="form-check-input"
                      type="radio"
                      v-model="form.isShowed"
                      id="discount_type-%"
                      value="1"
                    />
                    <label class="form-check-label" for="discount_type-%">
                      True
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div>
            <button
              type="submit"
              class="btn btn-main small mr-2"
              :disabled="is_loading"
            >
              <span v-if="!is_loading">SAVE</span>
              <span class="loading" v-else>
                <i class="fa fa-spinner fa-pulse fa-fw"></i>
              </span>
            </button>
            <router-link
              :to="{ name: 'customer_feedback', params: {} }"
              class="btn btn-main inverse small"
              >CANCEL</router-link
            >
          </div>

          <div class="loading"></div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "edit-customer-feedback",
  props: ["id"],
  components: {},
  computed: mapState(["user", "isAuthenticated"]),
  data() {
    return {
      profile: {},
      is_member: false,
      is_can_edit: false,
      is_load_data: false,
      is_loading: false,

      data: {},
      form: {
        isShowed: ""
      }
    };
  },
  mounted() {
    this.getAuthUser();
    this.getDetailComments();
  },
  methods: {
    getAuthUser() {
      if (this.isAuthenticated) {
        if (typeof this.user.id == typeof undefined || !this.user.id) {
          this.$http.get(process.env.API_URL + "user/profile").then(
            response => {
              this.profile = response.data.data;
              this.getDetailComments();
            },
            response => {}
          );
        } else {
          this.profile = this.user;
          this.getDetailComments();
        }
      }
    },
    getDetailComments() {
      this.is_load_data = true;
      this.$http.get(process.env.API_URL + "comments/" + this.id).then(
        response => {
          this.is_load_data = false;
          var status = response.data.status;
          if (status) {
            this.data = response.data.data;
            this.form.isShowed = this.data.isShowed;
          }
        },
        response => {}
      );
    },
    doUpdate() {
      this.is_loading = true;
      this.$http
        .post(process.env.API_URL + "comments/update/" + this.id, this.form)
        .then(
          response => {
            this.is_loading = false;
            var status = response.data.status;
            if (status) {
              this.$router.push({
                name: "customer_feedback",
                params: {}
              });
              this.$toastr("success", response.data.message);
            }
          },
          response => {}
        );
    }
  }
};
</script>

<style lang="css" scoped>
.radio-group input[type="radio"] {
  position: absolute;
  visibility: hidden;
  display: none;
}

.radio-group label {
  color: #ef3b29;
  display: inline-block;
  cursor: pointer;
  font-weight: bold;
  padding: 9px 20px;
  width: 100%;
  border: solid 1px #ef3b29;
}
.radio-group label::before {
  content: none;
}
.radio-group label::after {
  content: none;
}

.radio-group input[type="radio"]:checked + label {
  color: #fff;
  background: #ef3b29;
  border: solid 1px #ef3b29;
}

.radio-group label + input[type="radio"] + label {
  border: solid 1px #ef3b29;
}

.radio-group {
  overflow: hidden;
}
</style>



