<template>
    <div class="">
        <div class="mb-4">
            <label class="page-header">RECOVERY</label>
            <label class="breadcrumb">Recovery List/Edit Recovery</label>
        </div>
        <label class="page-title">EDIT RECOVERY</label>
        <div class="row">
            <div class="col-12 col-sm-11 col-md-10 col-lg-8">
                <div class="" v-if="is_load_data">
                    <i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i>
                </div>
                <form v-else class="mt-3" @submit.prevent="doUpdate()">
                    <div class="form-group mb-45">
                        <label>BANNER IMAGE <span class="red">* (375 x 214px)</span></label>
                        <div class="upload-wrapper continue mt-2">
                            <button type="button" class="btn btn-upload btn-main small">UPLOAD IMAGE</button>
                            <span class="ellipsis" v-if="files_name">{{files_name}}</span>
                            <span v-else class="ellipsis">No Choosen Files</span>
                            <input type="file" value="" @change="onChangeFile($event, 'files', '')" class="form-control hidden-input" accept="image/*">
                        </div>
                    </div>

                    <div class="form-group mb-45">
                        <label>CLASS TITLE <span class="red">*</span> </label>
                        <input type="text" class="form-control" v-model="form.name" required>
                    </div>
             

                    <div class="form-group mb-45">
                        <label>ICON <span class="red">* (50 x 48px)</span></label>
                        <div class="upload-wrapper continue mt-2">
                            <button type="button" class="btn btn-upload btn-main small">UPLOAD ICON</button>
                            <span class="ellipsis" v-if="logo.name">{{logo.name}}</span>
                            <span v-else class="ellipsis">No Choosen File</span>
                            <input type="file" value="" @change="onChangeFile($event, 'logo', '')" class="form-control hidden-input" accept="image/*">
                        </div>
                    </div>

                    <div class="form-group mb-45">
                        <label>DESCRIPTION <span class="red">*</span> </label>
                        <textarea type="text" class="form-control" v-model="form.description"></textarea>
                    </div>
                    <div class="form-group mb-45">
                        <label>CANCELLATION POLICY <span class="red">*</span> </label>
                        <textarea type="text" class="form-control" v-model="form.cancellationPolicy" required></textarea>
                    </div>
                    <div class="form-group mb-45">
                        <label>PREPARE <span class="red">*</span> </label>
                        <textarea type="text" class="form-control" v-model="form.prepare" required></textarea>
                    </div>
                    <div class="form-group mb-5">
                        <label>PRICE  <span class="red">*</span> </label>
                        <div class="input-group back">
                            <span class="back">CREDIT</span>
                            <input type="text" class="form-control" v-model="form.price" @keyup="formatPrice(form.price)" required>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-12 col-md-10 col-lg-8 col-xl-7">
                            <div class="form-group mb-5">
                                <label>DURATION  <span class="red">*</span> </label>
                                <div class="input-group back">
                                    <input type="number" class="form-control" min="1" v-model="form.duration" required style="padding-right: 60px;">
                                    <span class="back">minutes</span>
                                </div>
                            </div>
                        </div>
                    </div>
            
                    <div class="row">
                        <div class="col-6 col-sm-4 mb-2">
                            <div class="form-group mb-5">
                                <label>LOYALTY POINTS  <span class="red">*</span> </label>
                                <div class="input-group back">
                                    <input type="number" class="form-control" v-model="form.rewardPoints" required>
                                    <span class="back">pts</span>
                                </div>
                            </div>
                        </div>
                    </div>
 <div  class="form-group mb-45">
                      <label>IS ACTIVE <span class="red">*</span> </label>
                     <div class="row margin-left-5">
                        <div class="mt-2">
                                    <div class="form-check form-check-inline mb-1">
                                        <input
                                        class="form-check-input"
                                        type="radio"
                                        v-model="form.isActive"
                                        id="discount_type-%"
                                        value="1"
                                        />
                                        <label class="form-check-label" for="discount_type-%">
                                        YES
                                        </label>
                                    </div>
                                </div>
                                <div class="mt-2">
                                     <div class="form-check form-check-inline mb-1">
                                            <input
                                            class="form-check-input"
                                            type="radio"
                                            v-model="form.isActive"
                                            id="discount_type-rp"
                                            value="0"
                                            />
                                        <label class="form-check-label" for="discount_type-rp">
                                        NO
                                        </label>
                                    </div>
                                </div>
                                
                        
                        </div>
                </div>

                    <div class="">
                        <button type="submit" class="btn btn-main small mr-2" :disabled="is_loading">
                            <span v-if="!is_loading">SAVE</span>
                            <span class="loading" v-else>
                                <i class="fa fa-spinner fa-pulse fa-fw"></i>
                            </span>
                        </button>
                        <router-link :to="{ name: 'detail_recovery', params: {id: id} }" class="btn btn-main inverse small">CANCEL</router-link>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'

    export default {
        name: 'edit-recovery',
        props: ['id'],
        components: {
        },
        computed: mapState({
            user: 'user',
            isAuthenticated: 'isAuthenticated',
            files_name() {
                return this.files.name;
            }
        }),
        data (){
            return {
                IMAGE_URL: process.env.IMAGE_URL,
                is_load_data: false,
                is_loading: false,
                files: '',
                logo: '',
                options: [],
                load_class: false,
                isLoading: false,
                sportsClass: {},
                form: {
                  name: '',
                    subTitle: '',
                    description: '',
                    duration: '',
                    quota: '',
                    rewardPoints: '',
                    cancellationPolicy: '',
                    prepare: '',
                    price: '',
                     isActive : ""
                }
            }
        },
        mounted(){
            this.getAuthUser()
            this.getDetailRecovery()
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                            this.checkAuthority()
                        }, response => {})
                    } else{
                        this.profile = this.user
                        this.checkAuthority()
                    }
                }
            },
            checkAuthority(){
                if(this.profile.isOperator){
                    this.$toastr('error', "You don't have authorization to access this page!")
                    this.$router.push({ name: 'recovery', params: {} })
                }
            },
            getDetailRecovery(){
                this.is_load_data = true
                this.$http.get(process.env.API_URL + 'recovery/' + this.id).then(response => {
                    this.is_load_data = false
                    if(response.status == 404){
                        this.$toastr('error', response.statusText)
                        this.$router.push({ name: 'class', params: {} })
                    } else if(response.status == 200){
                        var status = response.data.status
                        if(status){
                            this.sportsClass = response.data.data;
                            this.form.name = this.sportsClass.name
                            this.form.subTitle = this.sportsClass.subTitle
                            this.form.description = this.sportsClass.description
                            this.form.quota = this.sportsClass.quota
                            this.form.duration = this.sportsClass.duration
                            this.form.rewardPoints = this.sportsClass.rewardPoints
                            this.form.cancellationPolicy = this.sportsClass.cancellationPolicy
                            this.form.prepare = this.sportsClass.prepare
                            this.form.price = this.formatNumber(this.sportsClass.price)
                            this.files = { name: this.sportsClass.image }
                            this.logo = { name: this.sportsClass.sportsClassAsset.logo }
                              this.form.isActive = this.sportsClass.isActive
                            // if (this.sportsClass.sportsClassAsset) {
                            //     this.form.color = this.sportsClass.sportsClassAsset.color;
                            // }
                        }
                    }
                }, response => {})
            },
            onChangeFile(event, attribute, defaultValue) {
                var files = event.target.files || event.dataTransfer.files;
                const MAX_SIZE_FILE = 2*1024*1024
                if (files[0].size > MAX_SIZE_FILE) {
                  this.$toastr('error', 'file size max 2Mb')
                  return
                }
                if (files.length) {
                    this[attribute] = defaultValue
                    if (Array.isArray(defaultValue)){
                        for (let i = 0; i < files.length; i++){
                            this[attribute][i] = files[i]
                        }
                    } else {
                        this[attribute] = files[0]
                    }
                }
            },
            doUpdate(){
                this.is_loading = true

                var formData = new FormData();
                formData.append('name', this.form.name)
                formData.append('subTitle', this.form.subTitle)
                formData.append('description', this.form.description)
                formData.append('cancellationPolicy', this.form.cancellationPolicy)
                formData.append('prepare', this.form.prepare)
                formData.append('duration', this.form.duration)
                formData.append('quota', this.form.quota)
                formData.append('price', parseInt(this.form.price.toString().replace(/\./g,'')))
                formData.append('rewardPoints', this.form.rewardPoints)
                formData.append('images', this.files.size? this.files : '' )
                formData.append('logo', this.logo.size? this.logo : '')
                    formData.append('isActive', this.form.isActive)

                this.$http.post(process.env.API_URL + 'recovery/update/additional/' + this.id, formData).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.$router.push({ name: 'recovery', params: {} })
                        this.$toastr('success', response.data.message)
                    }
                }, response => {})
            },
            formatPrice(value){
                this.form.price = this.formatNumber(value)
            },
            formatNumber(value){
                let temp = value.toString().replace(/\./g,'')
                let result = temp.replace(/[^0-9,]/g, "").replace(/(\d)(?=(\d\d\d)+(?!\d))/g, "$1.")
                return result
            },
        }
    }
</script>



