<template lang="html">
    <div class="">
        <div class="mb-4">
            <label class="page-header">USER</label>
            <label class="breadcrumb">User List</label>
        </div>

        <div class="row justify-content-between align-items-center mb-2">
            <div class="col-12 col-sm-6 col-md-5 col-lg-4 col-xl-3 mb-3">
                <router-link :to="{ name: 'create_user', params: {} }" class="btn btn-main inverse small">+ CREATE USER</router-link>
            </div>
            <div class="col-12 col-sm-6 col-md-5 col-lg-3 mb-3">
                <form class="" @submit.prevent="search()">
                    <div class="form-group search">
                        <input type="text" class="form-control small search" placeholder="search" v-model="keyword" @keyup="search()">
                        <span class="icon-search">
                            <img src="~@/assets/images/ic_search.svg" alt="">
                        </span>
                    </div>
                </form>
            </div>
        </div>
        <div class="mb-45">
            <button type="button" v-if="selected_type === 'member'" class="btn btn-tab inverse small mt-1 mb-2" @click="changeTab('admin')" >ADMIN</button>
            <button  v-else type="button" class="btn btn-tab active small mt-1 mb-2">ADMIN</button>

            <button v-if="selected_type === 'admin'" type="button" class="btn btn-tab inverse small mt-1 mb-2" @click="changeTab('member')" >MEMBER</button>
            <button v-else type="button" class="btn btn-tab active small mt-1 mb-2" >MEMBER</button>
    </div>

        <div class="row">
            <div class="col-12">
                <div class="table-responsive">
                    <table class="table">
                        <thead>
                            <tr>
                                <th  @click="doSortTable('firstName')">FULL NAME</th>
                                <th @click="doSortTable('role')">ROLE</th>
                                <th @click="doSortTable('phone')">PHONE NUMBER</th>
                                <th style="width: 4%;" @click="doSortTable('email')">EMAIL</th>
                                <th style="width: 16%;" @click="doSortTable('dateOfBirth')">DATE OF BIRTH</th>
                                <th  @click="doSortTable('gender')">GENDER</th>
                                 <th >REMAINING CREDIT</th>
                                   <th >LOYALTY POINTS</th>
                                <th >MEMBERSHIP NUMBER</th>
                                 <th >MEMBERSHIP STATUS</th>
                                <th @click="doSortTable('verification')">VERIFICATION STATUS</th>
                                <th >MEMBER STATUS</th>
                            </tr>
                        </thead>
                        <tbody v-if="is_loading">
                            <tr>
                                <td colspan="7" class="text-center">
                                    <span ><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                </td>
                            </tr>
                        </tbody>
                        <tbody v-else>
                            <tr class="text-center" v-if="data.length <= 0">
                                <td colspan="7">
                                    <span >No matching records found</span>
                                </td>
                            </tr>
                            <tr v-for="(item, index) in data" v-else>
                                <td>
                                    <router-link :to="{ name: 'detail_user', params: {id: item.id} }" class='link'>{{item.firstName}} {{item.lastName}}</router-link>
                                </td>
                                <td v-if="item.isAdmin">Super Admin </td>
                                <td v-if="item.isContentAdmin">Content Admin </td>
                                <td v-if="item.isMember"> Member  </td>
                                <td v-if="item.isManager"> Manager  </td>
                                <td v-if="item.isOperator"> Operator  </td>
                                <td v-if="item.isCoachAdmin"> Coach Admin </td>
                                <td v-if="item.isFrontdeskAdmin">Frontdesk Admin'</td>
                                <td>
                                    <span v-if="item.isMember">{{item.member.phone}}</span>
                                    <span v-else>-</span>
                                </td>
                                <td>
                               
                                         {{item.email}}
                            
                                   </td>
                                <td>
                                    <span v-if="item.isMember">{{item.member.dateOfBirth | dateFormatFull}}</span>
                                    <span v-else>-</span>
                                </td>
                                <td>
                                    <span v-if="item.isMember">{{item.member.gender ? item.member.gender : '-'}}</span>
                                     <span v-else >-</span>
                                </td>
                                 <td>
                                    <span v-if="item.isMember && item.member.remainingCredit > 0">{{item.member.remainingCredit}}</span>
                                    <span v-else >-</span>
                                </td>
                                 <td>
                                    <span v-if="item.isMember && item.member.rewardPoints > 0">{{item.member.rewardPoints}}</span>
                                    <span v-else >-</span>
                                </td>
                                 <td>
                                    <span v-if="item.isMember && item.member.membershipCode.length > 0">{{item.member.membershipCode[0].membershipCode}}</span>
                                    <span v-else >-</span>
                                </td>
                                 <td>
                                    <span v-if="item.isMember && item.member.membershipCode.length > 0" >true</span>
                                     <span v-else >false</span>
                                </td>
                                <td>
                                    <span v-if="item.isMember">
                                        <span v-if="item.member.memberVerified" class="font-success">Verified</span>
                                        <span v-else class="font-error">Unverified</span>
                                    </span>
                                    <span v-else >-</span>
                                </td>
                                <td >
                                    <span v-if="item.isMember">
                                        <span v-if=" item.isStatusUser" >Active</span>
                                        <span v-else >Deleted</span>
                                    </span>
                                    <span v-else >-</span>
                                </td>
                             
                            </tr>
                        </tbody>
                    </table>
                </div>

                <div class="">
                    <nav aria-label="Page navigation" class="page navigation" v-if="last_page > 1">
                        <paginate
                            :page-count="last_page"
                            :click-handler="callbackPaginate"
                            :prev-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                            :next-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                            :container-class="'pagination'"
                            :page-class="'page-item'"
                            :page-link-class="'page-link'"
                            :prev-class="'page-item'"
                            :prev-link-class="'page-link'"
                            :next-class="'page-item'"
                            :next-link-class="'page-link'"
                            :page-range="5"
                            :margin-pages="4"
                            ref="paginate">
                        </paginate>
                    </nav>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import arrow from '@/assets/images/ic_arrow_square_hover.svg'
import debounce from 'debounce'
import Paginate from 'vuejs-paginate'
import { mapState } from 'vuex'

    export default {
        name: 'user-list',
        components: {
            'paginate': Paginate
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        data (){
            return {
                profile: {},
                keyword: '',
                sort_column: 'email', // * fullName * role * email * phoneNumber *
                sort_type: 'asc',
                is_loading: false,
                data: [],
                last_page: 0,
                current_page: 0,
                arrow: arrow,
                selected_type : "admin"
            }
        },
        mounted(){
            this.getAuthUser()
            this.getListUser(true)
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                        }, response => {})
                    } else{
                        this.profile = this.user
                    }
                }
            },
            callbackPaginate: function(pageNum) {
                this.getListUser(true, pageNum, this.sort_column, this.sort_type)
            },
            search: debounce(function(){
                this.getListUser(false)
            },250),
            doSortTable(value){
                if(value == this.sort_column){
                    if(this.sort_type == 'asc'){
                        this.sort_type = 'desc'
                    } else {
                        this.sort_type = 'asc'
                    }
                } else {
                    this.sort_type = 'asc'
                }
                this.sort_column = value
                this.getListUser(true, null, this.sort_column, this.sort_type)
            },
            changeTab(tab) {
                this.selected_type= tab
                this.getListUser(true)
            },
            getListUser(loading, page = null, sortColumn = null, sortType = null){
                this.is_loading = loading

                this.$http.get(process.env.API_URL + 'user', {params: {'keyword': this.keyword, 'page': page,type:this.selected_type, 'sortColumn': sortColumn, 'sortType': sortType}}).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.data = response.data.data.data
                        this.last_page = response.data.data.last_page
                        this.current_page = response.data.data.current_page
                    }
                }, response => {})
            },
        }
    }
</script>

<style lang="css" scoped>
    .table thead th{
        cursor: pointer;
    }
    .table th, .table td{
        font-size: 11px;
    }
        .btn-tab{
    background-color: #ef3b29;
    font-size: 16px;
    letter-spacing: 1px;
    color: #fff;
    }
    .btn-tab:hover{
    color: #fff;
    box-shadow: 0 0 5px 2px #ef3b29;
    }
    .btn-tab.inverse{
    background-color: transparent;
    color: #ef3b29;
    font-size: 14px;
    border: solid 1px #ef3b29;
    }
  .btn-tab.active{
    background-color: #ef3b29;
    color: #ffffff;
    font-size: 14px;
    border: solid 1px #ef3b29;
    }
    
</style>



