<template>
    <div class="">
        <div class="mb-4">
            <label class="page-header">SPORT CLASS TYPE</label>
            <label class="breadcrumb">Sport Class Type/Edit Sport Class Type</label>
        </div>
        <label class="page-title">EDIT SPORT CLASS TYPE</label>
        <div class="row">
            <div class="col-12 col-sm-11 col-md-10 col-lg-8">
                <div class="" v-if="is_load_data">
                    <i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i>
                </div>
                <form v-else class="mt-3" @submit.prevent="doUpdate()">
                    <div class="form-group mb-45">
                        <label>IMAGE <span class="red">* (20 x 20px)</span> </label>
                        <div class="upload-wrapper continue mt-2">
                            <button type="button" class="btn btn-upload btn-main small">UPLOAD IMAGE</button>
                            <span class="ellipsis" v-if="file_name">{{file_name}}</span>
                            <span v-else class="ellipsis">No Choosen File</span>
                            <input type="file" value="" @change="onChangeFile" class="form-control hidden-input" accept="image/*">
                        </div>
                    </div>

                    <div class="form-group mb-45">
                        <label>NAME <span class="red">*</span> </label>
                        <input type="text" class="form-control" v-model="form.name" required>
                    </div>

                    <div class="mt-5">
                        <button type="submit" class="btn btn-main small mr-2" :disabled="is_loading">
                            <span v-if="!is_loading">SAVE</span>
                            <span class="loading" v-else>
                                <i class="fa fa-spinner fa-pulse fa-fw"></i>
                            </span>
                        </button>
                        <router-link :to="{ name: 'sport_type', params: {id: id} }" class="btn btn-main inverse small">CANCEL</router-link>
                    </div>

                    <div class="loading">

                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import { Datetime } from 'vue-datetime'

    export default {
        name: 'edit-sport-type',
        props: ['id'],
        components: {
            'datetime': Datetime
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        data (){
            return {
                profile: {},
                is_member: false,
                is_can_edit: false,
                is_load_data: false,
                file:null,
                is_loading: false,
                file_name: null,
                files: null,
                data: {},
                form: {
                    category:'',
                    title_1:'',
                    title_2:'',
                    description_1:'',
                    description_2:'',
                    id:''
                }
            }
        },
        mounted(){
            this.getAuthUser()
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    this.$http.get(process.env.API_URL + 'sport-class-type/' + this.id).then(response => {
                        const {data} = response.data
                        const {id,image, name} = data
                        this.form = {
                            id, image,name
                        }
                        this.file_name = data.image
                    }, response => {})
                } 
            },
            getRoleAuthUser(){
                if(this.profile.isAdmin){
                    this.form.role = 'Admin'
                } else if(this.profile.isManager){
                    this.form.role = 'Manager'
                } else if(this.profile.isOperator){
                    this.form.role = 'Operator'
                }
            },
            getDetailUser(){
                this.is_load_data = true
                this.$http.get(process.env.API_URL + 'user/' + this.id).then(response => {
                    this.is_load_data = false
                    var status = response.data.status
                    if(status){
                        this.data = response.data.data
                        this.is_member = this.data.isMember
                        this.form.name = this.data.name
                        this.form.id = this.data.id
                        // this.form.email = this.data.email
                    
                        if(this.profile.isOperator){
                            if(this.data.isOperator || this.data.isMember){
                                this.is_can_edit = true
                            } else {
                                this.is_can_edit = false
                            }
                        } else if(this.profile.isManager){
                            if(this.data.isManager || this.data.isOperator || this.data.isMember){
                                this.is_can_edit = true
                            } else {
                                this.is_can_edit = false
                            }
                        } else {
                            this.is_can_edit = true
                        }

                        if(!this.is_can_edit){
                            this.$toastr('error', "You don't have authorization to edit this user!")
                            this.$router.push({ name: 'users', params: {} })
                        }
                    }
                }, response => {})
            },
            doUpdate(){
                this.is_loading = true
                let formData = new FormData();
                if(this.file !== null){
                    formData.append('image', this.file)
                }
                // formData.append('image', this.form.image);
                formData.append('name', this.form.name);

                this.$http.post(process.env.API_URL + 'sport-class-type/update/' + this.id, formData, 
                {headers: {'Content-Type': 'multipart/form-data' }}).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        if(this.profile.id == this.data.id){
                            this.$parent.profile = response.data.data
                        }
                        this.$router.push({ name: 'sport_type', params: {id: this.id} })
                        this.$toastr('success', response.data.message)
                    }
                }, response => {})
            },
            onChangeFile(e) {
                var files = e.target.files || e.dataTransfer.files;
                if (!files.length)
                    return;
                this.file = files[0];
                this.file_name = files[0].name
            },
        }
    }
</script>

<style lang="css" scoped>
    .radio-group input[type=radio] {
        position: absolute;
        visibility: hidden;
        display: none;
    }

    .radio-group label {
        color: #ef3b29;
        display: inline-block;
        cursor: pointer;
        font-weight: bold;
        padding: 9px 20px;
        width: 100%;
        border: solid 1px #ef3b29;
    }
    .radio-group label::before{
        content: none;
    }
    .radio-group label::after{
        content: none;
    }

    .radio-group input[type=radio]:checked + label {
        color: #fff;
        background: #ef3b29;
        border: solid 1px #ef3b29;
    }

    .radio-group label + input[type=radio] + label{
        border: solid 1px #ef3b29;
    }

    .radio-group {
        overflow: hidden;
    }
</style>



