<template>
    <transition name="modal">
        <div class="modal-mask">
            <div class="modal-wrapper">
                <div class="modal-container">
                    <div class="modal-header mb-3">
                        <slot name="header">
                            default header
                        </slot>
                        <div class="modal-close" @click="$emit('close')">
                            <img src="~@/assets/images/ic_close.svg" alt="">
                        </div>
                    </div>

                    <slot name="body"> __________________ </slot>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
    // import { mixin as onClickOutside } from 'vue-on-click-outside'

    export default {
        name: 'modal',
        mixins: [],
        mounted(){},
        methods:{
            doClose(){
                this.$emit('close')
            },
        },
    }
</script>

<style scoped>
    .btn-main{
        min-width: 100px;
    }
    /*-----------------------------------------------------------------------------------*/
    /* Modal */
    /*-----------------------------------------------------------------------------------*/
    .modal-mask {
        position: fixed;
        z-index: 9999;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, .5);
        display: block;
        transition: all .3s ease;
        text-align: left;
        overflow-x: hidden;
        overflow-y: auto;
    }
    .modal-mask::-webkit-scrollbar {
        width: 8px;
        height: 8px;
    }
    .modal-mask::-webkit-scrollbar-track {
        box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    }
    .modal-mask::-webkit-scrollbar-thumb {
        background-color: #065f73;
        outline: 1px solid slategrey;
        -webkit-border-radius: 5px;
        border-radius: 5px;
    }
    .modal-wrapper {
        width: 50%;
        margin: 3rem auto;
    }
    .modal-container {
        width: 100%;
        padding: 30px 30px 30px 30px;
        background-color: #fff;
        border-radius: 10px;
        box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
        transition: all .3s ease;
        position: relative;
        text-align: center;
    }
    .modal-header {
        position: relative;
        justify-content: center;
        padding: 5px 0px;
        color: #ef3b29;
        font-family: "gothambold";
        font-size: 20px;
        letter-spacing: 1.43px;
        border-bottom: none;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
    }
    .modal-close{
        position: absolute;
        top: -22px;
        right: -14px;
        cursor: pointer;
    }
    .modal-close svg{
        width: 14px;
    }
    .modal-close svg .st0{
        fill: none;
        stroke: #aaaaaa;
        stroke-width: 2;
        stroke-linecap: round;
        stroke-linejoin: round;
        stroke-miterlimit: 10;
    }
    .modal-close:hover svg .st0{
        stroke: gray;
    }

    .modal-enter {
        opacity: 0;
    }
    .modal-leave-active {
        opacity: 0;
    }
    .modal-enter .modal-container,
    .modal-leave-active .modal-container {
        -webkit-transform: scale(1.1);
        transform: scale(1.1);
    }
</style>



