<template>
    <div class="row">
        <div class="col-12 col-md-11 col-lg-9">
            <div class="table-responsive">
                <table class="table">
                    <thead>
                        <tr>
                            <th>TRAINING DATE</th>
                            <th>CATEGORY TYPE</th>
                            <th>TRAINING</th>
                            <th>ATTENDANCE</th>
                            <th>BOOKING DATE</th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody v-if="is_loading">
                        <tr>
                            <td colspan="6" class="text-center">
                                <span ><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                            </td>
                        </tr>
                    </tbody>
                    <tbody v-else>
                        <tr class="text-center" v-if="data.length <= 0">
                            <td colspan="6">
                                <span >No matching records found</span>
                            </td>
                        </tr>
                        <tr v-for="item in data" :key="item.id">
                            <td> {{ item.session.start | dateFormat }} </td>
                              <td> {{ item.session.category }} </td>
                            <td v-if="item.session.category === 'personal trainer'"> {{ item.session.teacher.firstName }} {{ item.session.teacher.lastName }}  </td>
                            <td v-else> {{ item.session.sportsClass.name }} </td>
                            <td> {{ item.status }} </td>
                            <td> {{ item.createdAt | dateFormat }} </td>
                            <td></td>
                            <td></td>
                        </tr>
                    </tbody>
                </table>
            </div>

            <div class="">
                <nav aria-label="Page navigation" class="page navigation" v-if="last_page > 1">
                    <paginate
                        :page-count="last_page"
                        :click-handler="callbackPaginate"
                        :prev-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                        :next-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                        :container-class="'pagination'"
                        :page-class="'page-item'"
                        :page-link-class="'page-link'"
                        :prev-class="'page-item'"
                        :prev-link-class="'page-link'"
                        :next-class="'page-item'"
                        :next-link-class="'page-link'"
                        :page-range="5"
                        :margin-pages="4"
                        ref="paginate">
                    </paginate>
                </nav>
            </div>
        </div>
    </div>
</template>

<script>
    import Paginate from 'vuejs-paginate'
    import arrow from '@/assets/images/ic_arrow_square_hover.svg'

    export default {
        name: 'booking-history',
        props: ['id'],
        components: {
            'paginate': Paginate
        },
        data (){
            return {
                is_loading: false,
                data: [],
                last_page: 0,
                current_page: 0,
                arrow: arrow,
            }
        },
        mounted(){
            this.request()
        },
        methods: {
            callbackPaginate: function(pageNum) {
                this.request(pageNum)
            },
            request(page = null){
                this.is_loading = true
                this.$http.get(process.env.API_URL + 'member/booking-history', {params: {'memberID': this.id, 'page': page}}).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.data = response.data.data.data ? response.data.data.data : response.data.data
                        this.last_page = response.data.data.last_page ? response.data.data.last_page : 0
                        this.current_page = response.data.data.current_page ? response.data.data.current_page : 0
                    }
                }, response => {})
            }
        }
    }
</script>

<style scoped>

</style>



