<template lang="html">
    <div>
        <div class="mb-4">
            <label class="page-header">USER</label>
            <label class="breadcrumb">User List/User Detail</label>
        </div>

        <label class="page-title mr-4">USER PROFILE</label>
        <span action-button>
            <router-link :to="{ name: 'edit_user', params: {id: id} }" class="btn btn-main inverse small mr-2 mb-1" v-if="is_can_edit">Edit User</router-link>
            <button type="button" class="btn btn-main inverse small mb-1" v-if="data.id !== profile.id && profile.isAdmin" @click="delete_confirmation = true">Delete User</button>
        </span>

        <div class="detail-profile mt-4">
            <div class="row mb-4">
                <div class="col-12 col-sm-6 col-md-4 col-lg-4">
                    <div class="mb-4">
                        <label>EMAIL</label>
                        <div class="ellipsis" value>{{data.email}}</div>
                    </div>

                    <div class="mb-4">
                        <label>FULL NAME</label>
                        <div value>{{data.firstName}} {{data.lastName}}</div>
                    </div>

                    <div class="mb-4">
                        <label>PHONE NUMBER</label>
                        <div value>
                            <span v-if="is_member">{{data.member.phone}}</span>
                            <span v-else>---</span>
                        </div>
                    </div>
                </div>
                <div class="col-12 col-sm-6 col-md-4 col-lg-4">
                    <div class="mb-4">
                        <label>DATE OF BIRTH</label>
                        <div value>
                            <span v-if="is_member">{{data.member.dateOfBirth | dateFormatFull}}</span>
                            <span v-else>---</span>
                        </div>
                    </div>

                    <div class="mb-4">
                        <label>GENDER</label>
                        <div value>
                            <span v-if="is_member">{{data.member.gender}}</span>
                            <span v-else>---</span>
                        </div>
                    </div>

                    <div class="mb-4">
                        <label>EMERGENCY CONTACT</label>
                        <div value>
                            <span v-if="is_member">{{data.member.emergencyPhone}}</span>
                            <span v-else>---</span>
                        </div>
                    </div>
                </div>
                <div class="col-12 col-sm-6 col-md-4 col-lg-4">
                    <div class="mb-4">
                        <label>HOME ADDRESS</label>
                        <div value>
                            <address v-if="is_member">
                                {{data.member.address}}
                            </address>
                            <span v-else>---</span>
                        </div>
                    </div>
                </div>
            </div>

            <div class="tab" v-if="is_member">
                <ul class="mb-3">
                    <router-link :to="{ name: 'active_credit', params: {} }" tag="li">
                        <a href="#">Active Credit</a>
                    </router-link>
                    <router-link :to="{ name: 'active_package', params: {} }" tag="li">
                        <a href="#">Active Package</a>
                    </router-link>
                    <router-link :to="{ name: 'booking_history', params: {} }" tag="li">
                        <a href="#">Booking History</a>
                    </router-link>
                    <router-link :to="{ name: 'purchase_history', params: {} }" tag="li">
                        <a href="#">Purchase History</a>
                    </router-link>
                    <router-link :to="{ name: 'transaction_history', params: {} }" tag="li">
                        <a href="#">Transaction History</a>
                    </router-link>
                    
                </ul>

                <router-view/>
            </div>
        </div>

        <confirmation v-if="delete_confirmation" @close="delete_confirmation = false" @confirm="deleteUser()">
            <div slot="header">DELETE USER</div>
            <div slot="body">
                are you sure you want to delete this user? this action can not be reverted
            </div>
        </confirmation>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import Confirmation from '@/components/Confirmation'

    export default {
        name: 'detail-user',
        props: ['id'],
        components: {
            'confirmation': Confirmation
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        data(){
            return{
                profile: {},

                is_loading: false,
                is_member: false,
                is_can_edit: false,
                delete_confirmation: false,
                data: {},
            }
        },
        mounted(){
            this.getAuthUser()
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                            this.getDetailUser()
                        }, response => {})
                    } else{
                        this.profile = this.user
                        this.getDetailUser()
                    }
                }
            },
            getDetailUser(){
                this.is_loading = true
                this.$http.get(process.env.API_URL + 'user/' + this.id).then(response => {
                    this.is_loading = false
                    if(response.status == 404){
                        this.$toastr('error', response.statusText)
                        this.$router.push({ name: 'users', params: {} })
                    } else if(response.status == 200){
                        var status = response.data.status
                        if(status){
                            this.data = response.data.data
                            this.is_member = this.data.isMember
                            if(this.data.isMember){
                                this.$router.push({ name: 'active_credit' })
                            }

                            if(this.profile.isOperator){
                                if(this.data.isOperator || this.data.isMember){
                                    this.is_can_edit = true
                                } else {
                                    this.is_can_edit = false
                                }
                            } else if(this.profile.isManager){
                                if(this.data.isManager || this.data.isOperator || this.data.isMember){
                                    this.is_can_edit = true
                                } else {
                                    this.is_can_edit = false
                                }
                            } else {
                                this.is_can_edit = true
                            }
                        }
                    }
                }, response => {})
            },
            deleteUser(){
                this.$http.post(process.env.API_URL + 'user/delete/' + this.id).then(response => {
                    this.delete_confirmation = false
                    var status = response.data.status
                    if(status){
                        this.$toastr('success', response.data.message)
                        this.$router.push({ name: 'users' })
                    }
                }, response => {})
            },
        }
    }
</script>

<style lang="css" scoped>
    .detail-profile{
        text-align: left;
    }
    .detail-profile label{
        margin-bottom: .1rem;
        font-size: 12px;
        letter-spacing: .75px;
    }
    .detail-profile [value]{
        font-size: 14px;
        font-family: 'gothammedium';
    }
    @media (max-width: 576px) {
        .detail-profile{
            text-align: center;
        }
    }

    @media (max-width: 480px) {
        [action-button]{
            display: block;
        }
    }

    .tab ul{
        padding-left: 0;
        list-style: none;
    }
    .tab ul li{
        display: inline-block;
    }
    .tab ul li{
        padding: 5px 15px;
    }
    .tab ul li a{
        color: #ef3b29;
    }
    .tab ul li.active{
        background-color: #ef3b29;
        border-radius: 25px;
        padding: 5px 15px;
    }
    .tab ul li.active a{
        color: #fff;
    }
    .tab ul li a:not(.active):hover{
        opacity: .7;
    }
    .tab ul li a.active:hover{
        box-shadow: 0 0 3px 1px #ef3b29;
    }
</style>



