<template>
    <div class="">
        <div>
            <label class="page-header">CLASS</label>
            <label class="breadcrumb">Class List</label>
        </div>
   <div class="mb-2">
            <label class="page-header">SUBCONTENT</label>
        </div>

        <div class="table-responsive mb-4">
                    <table class="table">
                        <thead>
                            <tr>
       
                                <th>TITLE</th>
                                <th>DESCRIPTION</th>
                           
                                <th style="width: 24%;">ACTION</th>
                            </tr>
                        </thead>
                        <tbody v-if="is_loading">
                            <tr>
                                <td colspan="8" class="text-center">
                                    <span ><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                </td>
                            </tr>
                        </tbody>
                        <tbody  v-else>
                          
                               <tr class="text-center" v-if="is_loading">
                                <td colspan="8">
                                    <span><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                </td>
                            </tr>
                            <tr  v-else>
                           
                                <td>{{datasubcontent.title}}</td>
                                <td>{{datasubcontent.description}}</td>
                           
                              
                                <td>
                                    <router-link :to="{ name: 'edit_subcontent', params: {id: datasubcontent.id} }" class="btn btn-main inverse small mt-1 mb-1">EDIT</router-link>
                                  
                                </td>
                            </tr>
                        </tbody>
                    </table>
         </div>
     
        
  
        <div class="row justify-content-between align-items-center mb-4 mt-2">
            <div class="col-12 col-sm-6 col-md-5 col-lg-4 col-xl-3 mb-3">
                <router-link :to="{ name: 'create_class', params: {} }" class="btn btn-main inverse small" :class="{'disabled': profile.isOperator}">+ CREATE CLASS</router-link>
            </div>
            <div class="col-12 col-sm-6 col-md-5 col-lg-3 mb-3">
                <form class="" @submit.prevent="search()">
                    <div class="form-group search">
                        <input type="text" class="form-control small search" placeholder="search" v-model="keyword" @keyup="search()">
                        <span class="icon-search">
                            <img src="~@/assets/images/ic_search.svg" alt="">
                        </span>
                    </div>
                </form>
            </div>
        </div>

        <div class="row">
            <div class="col-12 col-sm-11 col-md-12 col-lg-10 col-xl-8">
                <div class="card loading mb-4" v-if="is_loading">
                    <i class="fa fa-spinner fa-pulse fa-3x fa-fw"></i>
                </div>
                <div class="row mb-4 align-items-center" v-else v-for="(item, index) in data" :key="index" :to="{ name: 'detail_class', params: {id: item.id} }">
                         <div class="col-10 col-sm-11">
                                <router-link :to="{ name: 'detail_class', params: {id: item.id} }">
                    <div class="card mb-4">
                        <div class="row no-gutters">
                            <div class="col-md-3 card-image" :style="{ backgroundImage: 'url('+item.imageUrl+')' }">

                            </div>
                            <div class="col-md-9">
                                <div class="card-body">
                                    <h5 class="card-title">{{item.name}}</h5>
                                    <table>
                                        <thead>
                                            <th style="width: 63%;">Description</th>
                                            <th>Duration</th>
                                          
                                        </thead>
                                        <tbody>
                                            <td>
                                                <div class="desc">
                                                    {{item.description}}
                                                </div>
                                            </td>
                                            <td>{{ item.duration | numberFormat }} minutes</td>
                                          
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                                </router-link>
                         </div>
                        <div class="col-1 text-center">
                        <div class="">
                            <div class="mb-3">
                                <a href="#" @click.prevent="changeOrder(true, item, index, item.priority)" :class="{'disabled': item.priority == 1 || keyword !== ''}">
                                    <i class="fa fa-arrow-up fa-2x" aria-hidden="true"></i>
                                </a>
                            </div>
                            <div>
                                <span class="font-bold underline"> &nbsp; {{ item.priority }} &nbsp; </span>
                            </div>
                            <div v-if="item.priority === totaldata" class="mt-3">
                                <a href="#" @click.prevent="changeOrder(false, item, index, item.priority)" :class="{'disabled': item.priority == totaldata || keyword !== ''}">
                                    <i class="fa fa-arrow-down fa-2x" aria-hidden="true"></i>
                                </a>
                            </div>
                            <div v-else class="mt-3">
                                <a href="#" @click.prevent="changeOrder(false, item, index, item.priority)" :class="{'disabled':  keyword !== ''}">
                                    <i class="fa fa-arrow-down fa-2x" aria-hidden="true"></i>
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
              
                <div class="">
                    <nav aria-label="Page navigation" class="page navigation" v-if="last_page > 1">
                        <paginate
                            :page-count="last_page"
                            :click-handler="callbackPaginate"
                            :prev-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                            :next-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                            :container-class="'pagination'"
                            :page-class="'page-item'"
                            :page-link-class="'page-link'"
                            :prev-class="'page-item'"
                            :prev-link-class="'page-link'"
                            :next-class="'page-item'"
                            :next-link-class="'page-link'"
                            :page-range="5"
                            :margin-pages="4"
                            ref="paginate">
                        </paginate>
                    </nav>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import debounce from 'debounce'
    import Paginate from 'vuejs-paginate'
    import arrow from '@/assets/images/ic_arrow_square_hover.svg'

    export default {
        name: 'class-list',
        components: {
          'paginate': Paginate
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        data (){
            return {
                IMAGE_URL: process.env.IMAGE_URL,
                profile: {},
                keyword: '',
                is_loading: false,
                data: [],
                datasubcontent: [],
                last_page: 0,
                totaldata: 0,
                current_page: 0,
                arrow: arrow,
                sort_column: '', // * fullName * role * email * phoneNumber *
                sort_type: '',
            }
        },
        mounted(){
            this.getAuthUser()
            this.getListClass(true)
              this.getListsubcontent(true)
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                        }, response => {})
                    } else{
                        this.profile = this.user
                    }
                }
            },
            search: debounce(function(){
                this.getListClass(false)
            },250),
            callbackPaginate: function(pageNum) {
                this.getListClass(true, pageNum, this.sort_column, this.sort_type)
            },
            getListsubcontent(loading, page = null, sortColumn = null, sortType = null){
                this.is_loading = loading

                this.$http.get(process.env.API_URL + 'subcontent/1', {params: {'keyword': this.keyword, 'page': page, 'sortColumn': sortColumn, 'sortType': sortType}}).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.datasubcontent = response.data.data
                 
                    }
                }, response => {})
            },
            changeOrder(isUp, item, index, priority){
           
                var temp = {}
                var positionTarget = index
                var status = false

                if(this.keyword == ''){
                    if(this.current_page === 1) {
               
                    if(isUp){
                        positionTarget = index - 1
                        if(index > 0){
                            status = true
                        }
                    } else {
                        positionTarget = index + 1
                        if(index < this.totaldata - 1){
                            status = true
                        }
                    }
                    }else {
                     
                    if(isUp){
                        var total_index = index + (10 * (this.current_page - 1 ))
                        positionTarget = total_index - 1
                        if(total_index > 0){
                            status = true
                        }
                    } else {
                        positionTarget = total_index + 1
                        if(total_index < this.data.length - 1){
                            status = true
                        }
                    }
                    }
                   
        
                    if(status){
                        this.$http.post(process.env.API_URL + 'sports-class/update/'+ item.id, {priority: positionTarget + 1}).then(response => {
                            var status = response.data.status
                            if(status){
                             
                                // temp = this.data[positionTarget]
                                // this.$set(this.data, positionTarget, this.data[index])
                                // this.$set(this.data, index, temp)
                                this.$toastr('success', response.data.message)
                                // this.data[positionTarget].priority = response.data.data.priority
                                // this.data[index].priority = index + 1
                                this.getListClass(true, this.current_page, this.sort_column, this.sort_type)
                               
                                

                            }
                        }, response => {})
                    }
                }
            },
            getListClass(loading, page = null, sortColumn = null, sortType = null){
                this.is_loading = loading
                this.$http.get(process.env.API_URL + 'sports-class', {params: {'keyword': this.keyword, 'page': page, 'sortColumn': sortColumn, 'sortType': sortType}}).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.data = response.data.data.data
                        this.totaldata = response.data.data.total
                     
                        this.last_page = response.data.data.last_page
                        this.current_page = response.data.data.current_page
                    }
                }, response => {})
            },
        }
    }
</script>

<style lang="css" scoped>
    a{
        color: #606060;
    }
    a:hover .card{
        box-shadow: 0 0 6px 3px #b5b5b5;
    }
    .card.loading{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    table th,
    table td{
      
        vertical-align: top;
    }
    table th:first-child,
    table td:first-child{
        padding-left: 0;
    }
    table th:last-child,
    table td:last-child{
        padding-right: 0;
    }
    table thead th{
        font-size: 14px;
        color: #ef3b29;
        font-family: 'gothambold';
    }
    table tbody td{
        font-size: 12px;
    }
     .underline{
        border-bottom: solid 1px #ef3b29;
        padding-bottom: 3px;
    }
      a{
        color: #ef3b29;
    }
     a:hover{
        opacity: .7;
    }
    a.disabled{
        color: #9b9b9b;
        cursor: not-allowed;
    }
     label.title{
        font-size: 14px;
        color: #ef3b29;
        font-family: 'gothambold';
    }
    p{
        color: #000;
    }

    @media (max-width: 480px) {
        [action-button]{
            display: block;
        }
    }
</style>



