<template lang="html">
    <div class="">
        <div class="mb-4">
            <label class="page-header">PACKAGE</label>
            <label class="breadcrumb">Package List</label>
        </div>

        <div class="row justify-content-between align-items-center mb-2">
       
            <!-- <div class="col-12 col-sm-6 col-md-5 col-lg-3 mb-3">
                <form class="" @submit.prevent="search()">
                    <div class="form-group search">
                        <input type="text" class="form-control small search" placeholder="search" v-model="keyword" @keyup="search()">
                        <span class="icon-search">
                            <img src="~@/assets/images/ic_search.svg" alt="">
                        </span>
                    </div>
                </form>
            </div> -->
        </div>

    
        <div class="row">
            <div class="col-12">
                <div class="table-responsive">
                    <table class="table">
                        <thead>
                            <tr>
                                <th>PREVIEW</th>
                                <th @click="doSortTable('name')">PACKAGE TITLE</th>
                                <th @click="doSortTable('expiry')">ACTIVE PERIOD</th>
                                <th @click="doSortTable('price')">PRICE</th>
                                <th @click="doSortTable('firstTimeOnly')">FIRST TIME</th>
                                <th @click="doSortTable('month_class_package')">SUBSCRIBE</th>
                                <th>IS PACKAGE ACTIVE</th>
                                <th >ACTION</th>
                            </tr>
                        </thead>
                        <tbody v-if="is_loading">
                            <tr>
                                <td colspan="8" class="text-center">
                                    <span ><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                </td>
                            </tr>
                        </tbody>
                        <tbody v-else>
                            <tr class="text-center" v-if="data.length <= 0">
                                <td colspan="8">
                                    <span >No matching records found</span>
                                </td>
                            </tr>
                            <tr v-for="(item, index) in data" v-else>
                                <td><img :src="item.imageUrl" :alt="item.name" class="img-thumbnail" style="width: 120px;" /></td>
                                <td>
                                    <router-link :to="{ name: 'detail_package', params: {id: item.id} }" class='link'>{{item.name}}</router-link>
                                </td>
                
                                <td>{{item.expiry}}</td>
                                <td>
                                    <span v-if="item.price_type == 'credit'"> {{item.price | numberFormat}} Credit</span>
                                    <span v-else>{{item.price | currencyFormat}}</span>
                                </td>
                                <td>{{item.firstTimeOnly > 0 ? 'YES' : 'NO'}}</td>
                                <td>{{item.month_class_package > 0 ? 'YES' : 'NO'}}</td>
                                  <td>
                  
                                    <span>{{ item.isShowed === 0 ? "NO" : "YES" }}</span>
                  
                                </td>
                                <td>
                                      <router-link :to="{ name: 'edit_package', params: {id: item.id} }" class="btn btn-main inverse small mr-2 mb-1">EDIT</router-link>
                                </td>
                               
                            </tr>
                        </tbody>
                    </table>
                </div>

                <div class="">
                    <nav aria-label="Page navigation" class="page navigation" v-if="last_page > 1">
                        <paginate
                            :page-count="last_page"
                            :click-handler="callbackPaginate"
                            :prev-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                            :next-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                            :container-class="'pagination'"
                            :page-class="'page-item'"
                            :page-link-class="'page-link'"
                            :prev-class="'page-item'"
                            :prev-link-class="'page-link'"
                            :next-class="'page-item'"
                            :next-link-class="'page-link'"
                            :page-range="5"
                            :margin-pages="4"
                            ref="paginate">
                        </paginate>
                    </nav>
                </div>
            </div>
        </div>

        <confirmation v-if="delete_confirmation" @close="delete_confirmation = false" @confirm="deletePackage()">
            <div slot="header">DELETE PACKAGE</div>
            <div slot="body">
                are you sure you want to delete this package? this action can not be reverted
            </div>
        </confirmation>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import { Datetime } from 'vue-datetime'
    import Paginate from 'vuejs-paginate'
    import debounce from 'debounce'
    import Confirmation from '@/components/Confirmation'
    import arrow from '@/assets/images/ic_arrow_square_hover.svg'

    export default {
        name: 'package-list',
        components: {
            'paginate': Paginate,
            'confirmation': Confirmation,
            'datetime': Datetime
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        data (){
            return {
                profile: {},
                keyword: '',
                is_loading: false,
                data: [],
                last_page: 2,
                current_page: 0,
                start_date: '',
                end_date: '',
                show_confirmation: false,
                delete_confirmation: false,
                data_temp: {},
                index_temp: '',
                arrow: arrow,
            }
        },
        mounted(){
            this.getAuthUser()
            this.getListPackage(true)
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                        }, response => {})
                    } else{
                        this.profile = this.user
                    }
                }
            },
            callbackPaginate: function(pageNum) {
                this.getListPackage(true, pageNum, this.sort_column, this.sort_type)
            },
            search: debounce(function(){
                this.getListPackage(false)
            },250),
            doSortTable(value){
                if(value == this.sort_column){
                    if(this.sort_type == 'asc'){
                        this.sort_type = 'desc'
                    } else {
                        this.sort_type = 'asc'
                    }
                } else {
                    this.sort_type = 'asc'
                }
                this.sort_column = value
                this.getListPackage(true, null, this.sort_column, this.sort_type)
            },
            getListPackage(loading, page = null, sortColumn = null, sortType = null){
                this.is_loading = loading
                this.$http.get(process.env.API_URL + 'package', {params: {'keyword': this.keyword, 'page': page, 'sortColumn': sortColumn, 'sortType': sortType, 'start': this.start_date, 'end': this.end_date}}).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.data = response.data.data.data
                        this.last_page = response.data.data.last_page
                        this.current_page = response.data.data.current_page
                    }
                }, response => {})
            },
            showDeleteConfirmation(data, index){
                this.data_temp = data
                this.index_temp = index
                this.delete_confirmation = true
            },
            deletePackage(){
                this.$http.post(process.env.API_URL + 'package/delete/' + this.data_temp.id).then(response => {
                    this.delete_confirmation = false
                    var status = response.data.status
                    if(status){
                        this.data.splice(this.index_temp, 1)
                        this.$toastr('success', response.data.message)
                    } else {
                        this.$toastr('error', response.data.message)
                    }
                }, response => {})
            },
        }
    }
</script>

<style lang="css" scoped>
    .table thead th{
        cursor: pointer;
    }
</style>



