<template lang="html">
    <div class="">
        <div class="mb-4">
            <label class="page-header">PACKAGE</label>
            <label class="breadcrumb">Package List/Edit Package</label>
        </div>
        <label class="page-title">EDIT PACKAGE</label>
        <div class="row">
            <div class="col-12 col-sm-11 col-md-10 col-lg-8">
                <form class="mt-3" @submit.prevent="doUpdate()">
                    <div v-if="is_showed" class="form-group mb-45">
                        <label>IMAGE <span class="red">* (375 x 214px)</span> </label>
                        <div class="upload-wrapper continue mt-2">
                            <button type="button" class="btn btn-upload btn-main small">UPLOAD IMAGE</button>
                            <span class="ellipsis" v-if="file_name">{{file_name}}</span>
                            <span v-else class="ellipsis">No Choosen File</span>
                            <input type="file" value="" @change="onChangeFile" class="form-control hidden-input" accept="image/*">
                        </div>
                    </div> 
                    <div class="form-group mb-45">
                        <label>PACKAGE TITLE  <span class="red">*</span> </label>
                        <input type="text" class="form-control" v-model="form.name" required>
                    </div>
                    <div class="form-group mb-45">
                        <label>DESCRIPTION <span class="red">*</span> </label>
                        <textarea type="text" class="form-control" v-model="form.description" required></textarea>
                    </div>
                 
                    <div class="row">
                        <div class="col-12 col-md-6">
                            <div class="form-group mb-5">
                                <label>PRICE  <span class="red">*</span> </label>
                                <div v-if="form.price_type == 'money'" class="input-group front">
                                    <span class="front">Rp</span>
                                 
                                    <input type="text" class="form-control" v-model="form.price" @keyup="formatPrice(form.price)" required>
                                </div>
                                 <div v-if="form.price_type == 'credit'"  class="input-group back">
                                  
                                    <span class="back">CREDIT</span>
                                    <input type="text" class="form-control" v-model="form.price" @keyup="formatPrice(form.price)" required>
                                </div>
                            </div>
                        </div>
                     
                    </div>
                    <div class="form-group mb-45">
                        <label>PACKAGE POLICY <span class="red">*</span> </label>
                        <textarea type="text" class="form-control" v-model="form.package_policy" required></textarea>
                    </div>
                    <div v-if="is_showed" class="form-group mb-45">
                      <label>IS PACKAGE ACTIVE <span class="red">*</span> </label>
                     <div class="row margin-left-5">
                        <div class="mt-2">
                                    <div class="form-check form-check-inline mb-1">
                                        <input
                                        class="form-check-input"
                                        type="radio"
                                        v-model="form.isShowed"
                                        id="discount_type-%"
                                        value="1"
                                        />
                                        <label class="form-check-label" for="discount_type-%">
                                        YES
                                        </label>
                                    </div>
                                </div>
                                <div class="mt-2">
                                     <div class="form-check form-check-inline mb-1">
                                            <input
                                            class="form-check-input"
                                            type="radio"
                                            v-model="form.isShowed"
                                            id="discount_type-rp"
                                            value="0"
                                            />
                                        <label class="form-check-label" for="discount_type-rp">
                                        NO
                                        </label>
                                    </div>
                                </div>
                                
                        
                        </div>
                    </div>

                    
                    <div class="">
                        <button type="submit" class="btn btn-main small mr-2" :disabled="is_loading">
                            <span v-if="!is_loading">SAVE</span>
                            <span class="loading" v-else>
                                <i class="fa fa-spinner fa-pulse fa-fw"></i>
                            </span>
                        </button>
                        <router-link :to="{ name: 'package', params: {} }" class="btn btn-main inverse small">CANCEL</router-link>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import { Datetime } from 'vue-datetime'

    export default {
        name: '',
        props: ['id'],
        components: {
            'datetime': Datetime,
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        data (){
            return {
                profile: {},
                IMAGE_URL: process.env.IMAGE_URL,
                file: null,
                file_name : '',
                expiry: null,
                expiry_options: ['15 days', '30 days', '45 days', '50 days', '60 days', '90 days', '100 days'],
                is_showed : false,
                is_load_data: false,
                is_loading: false,
                form: {
                    description : '',
                    package_policy : '',
                    price_type : '',
                    name: '',
                    price: '',
                    isShowed : 0
                }
            }
        },
        mounted(){
            this.getAuthUser()
            this.getDetailPackage()
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                        }, response => {})
                    } else{
                        this.profile = this.user
                    }
                }
            },
            calculatePricePerClass(){
                var price = parseInt(this.form.price.toString().replace(/\./g,''))
                if(this.form.price != ''){
                    this.form.pricePerItem = price / parseFloat(this.form.credit)
                    this.form.pricePerItem = this.form.pricePerItem.toString().split('.')[0]
                    this.formatPricePerClass(this.form.pricePerItem)
                }
            },
            formatPrice(value){
                var price = parseInt(value.toString().replace(/\./g,''))
                if(this.form.credit != ''){
                    this.form.pricePerItem = price / parseFloat(this.form.credit)
                    this.form.pricePerItem = this.form.pricePerItem.toString().split('.')[0]
                    this.formatPricePerClass(this.form.pricePerItem)
                }
                this.form.price = this.formatNumber(value)
            },
            formatPricePerClass(value){
                this.form.pricePerItem = this.formatNumber(value)
            },
            formatNumber(value){
                var temp = value.toString().replace(/\./g,'')
                var result = temp.replace(/[^0-9,]/g, "").replace(/(\d)(?=(\d\d\d)+(?!\d))/g, "$1.")
                return result
            },
            checkShareable(){
                if(!this.form.isShareable){
                    this.form.shareable = 0
                }
            },
            onChangeFile(e) {
                var files = e.target.files || e.dataTransfer.files;
                if (!files.length)
                    return;
                this.file = files[0];
                this.file_name = files[0].name
            },
            onChangefirstTimeOnly() {
               if(this.form.firstTimeOnly == 1){
                this.form.month_class_package = 0
                this.form.is_free_class = 0
                this.form.is_free_recovery = 0
               }else {
                 this.form.month_class_package = 1
               }
            },
            onChangeMonthClass() {
               if(this.form.month_class_package == 1){
                this.form.firstTimeOnly = 0
               }else {
                this.form.firstTimeOnly = 1
                this.form.is_free_class = 0
                this.form.is_free_recovery = 0
               }
            },
            getDetailPackage(){
                this.is_load_data = true
                this.$http.get(process.env.API_URL + 'package/' + this.id).then(response => {
                    this.is_load_data = false
                    if(response.status == 404){
                        this.$toastr('error', response.statusText)
                        this.$router.push({ name: 'package', params: {} })
                    } else if(response.status == 200){
                        var status = response.data.status
                        if(status){
                            this.form.name = response.data.data.name
                            this.form.description = response.data.data.description
                            this.form.package_policy = response.data.data.package_policy
                            this.form.price_type = response.data.data.price_type
                            this.file_name = response.data.data.image
                            this.form.price = response.data.data.price
                            this.form.isShowed = response.data.data.isShowed
                            this.formatPrice(this.form.price)
                            if(response.data.data.month_class_package == 1) {
                                this.is_showed = true
                            }
                        }
                    }
                }, response => {})
            },
            doUpdate(){
                this.is_loading = true
                this.form.price = parseInt(this.form.price.toString().replace(/\./g,''))
                var formData = new FormData();
                if(this.file !== null){
                    formData.append('image', this.file)
                }
                formData.append('name', this.form.name)
                formData.append('description', this.form.description)
                formData.append('package_policy', this.form.package_policy)
                formData.append('price', this.form.price)
                formData.append('isShowed', this.form.isShowed)
                this.$http.post(process.env.API_URL + 'package/update/' + this.id, formData).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.$router.push({ name: 'package', params: {} })
                        this.$toastr('success', response.data.message)
                    }
                }, response => {})
            }
        }
    }
</script>

<style lang="css" scoped>

</style>



