<template>
    <div class="">
        <div class="mb-4">
            <label class="page-header">TRAINER</label>
            <label class="breadcrumb">Trainer List/Create Trainer</label>
        </div>
        <label class="page-title">CREATE NEW TRAINER</label>
        <div class="row">
            <div class="col-12 col-sm-11 col-md-10 col-lg-8">
                <form class="mt-3" @submit.prevent="doCreate()">
                    <div class="form-group mb-4">
                        <label class="mb-2">TRAINER TYPE</label>
                        <div class="">
                            <div class="form-check form-check-inline mb-1">
                                <input class="form-check-input" type="radio" v-model="form.category" id="user_type_pt" value="personal trainer">
                                <label class="form-check-label" for="user_type_pt">
                                    PT (Personal Trainer)
                                </label>
                            </div>
                            <div class="form-check form-check-inline mb-1">
                                <input class="form-check-input" type="radio" v-model="form.category" id="user_type_class" value="class">
                                <label class="form-check-label" for="user_type_class">
                                    Class
                                </label>
                            </div>
                        </div>
                    </div>

                    <div class="row" >
                        <div class="col-12 col-md-6">
                            <div class="form-group mb-45">
                                <label>FIRST NAME <span class="red">*</span> </label>
                                <input type="text" class="form-control" v-model="form.firstName" required>
                            </div>
                        </div>
                        <div class="col-12 col-md-6">
                            <div class="form-group mb-45">
                                <label>LAST NAME  <span class="red">*</span> </label>
                                <input type="text" class="form-control" v-model="form.lastName" required>
                            </div>
                        </div>
                    </div>
                    <div v-if="form.category === 'personal trainer'" class="form-group mb-5">
                        <label>PRICE  <span class="red">*</span> </label>
                        <div class="input-group back">
                            <span class="back">CREDIT</span>
                            <input type="text" class="form-control" v-model="form.price" @keyup="formatPrice(form.price)" required>
                        </div>
                    </div>
                    <div  v-if="form.category === 'personal trainer'" class="form-group mb-45">
                        <label>LOYALTY POINTS <span class="red">*</span> </label>
                        <div class="row align-items-center">
                            <div class="col">
                                <input type="number" class="form-control" v-model="form.rewardPoints" min="0" required />
                            </div>
                            <div class="col-auto">
                                Pts
                            </div>
                        </div>
                    </div>

                    <div v-if="form.category === 'personal trainer'"  class="form-group mb-5">
                                <label>DURATION  <span class="red">*</span> </label>
                                <div class="input-group back">
                                    <input type="number" class="form-control" min="1" v-model="form.duration" style="padding-right: 60px;" required>
                                    <span class="back">minutes</span>
                                </div>
                    </div>

                     <div v-if="form.category === 'personal trainer'"  class="form-group mb-45">
                                    <label class="mb-2">SELECT LOCATION <span class="red">*</span></label>
                                    <multiselect v-model="selected_location" :options="location_options" @search-change="getListLocation" :searchable="true" track-by="id" label="location_name" :close-on-select="true" :show-labels="false" placeholder="SELECT LOCATION"></multiselect>
                    </div>
          
                    <div v-if="form.category === 'personal trainer'" class="form-group mb-45">
                        <label class="mb-2">SPECIALIZE</label>
                        <multiselect
                            v-model="form.specialization"
                            placeholder="SELECT SPECLIALIZE"
                            label="name" track-by="id"
                            :options="options"
                            :multiple="true"
                            :loading="isLoading"
                            :searchable="true"
                            :hide-selected="true"
                            :show-no-results="true"
                            @search-change="getSpecializeList">
                        </multiselect>
                    </div>
                    <div  v-if="form.category === 'personal trainer'" class="form-group mb-45">
                        <label>ABOUT <span class="red">*</span> </label>
                        <textarea type="text" class="form-control" v-model="form.about" required />
                    </div>
                    <div v-if="form.category === 'personal trainer'" class="form-group mb-45">
                        <label>IMAGE <span class="red">* (375 x 214px)</span> </label>
                        <div class="upload-wrapper continue mt-2">
                            <button type="button" class="btn btn-upload btn-main small">UPLOAD IMAGE</button>
                            <span v-if="file_name" class="ellipsis">{{ file_name }}</span>
                            <span v-else class="ellipsis">No Choosen Files</span>
                            <input type="file" value="" @change="onChangeFile($event, 'files', [])" class="form-control hidden-input" accept="image/*" required>
                        </div>
                    </div>

                    <div class="mt-5">
                        <button type="submit" class="btn btn-main small mr-2" :disabled="is_loading">
                            <span v-if="!is_loading">CREATE</span>
                            <span class="loading" v-else>
                                <i class="fa fa-spinner fa-pulse fa-fw"></i>
                            </span>
                        </button>
                        <router-link :to="{ name: 'trainer', params: {} }" class="btn btn-main inverse small">CANCEL</router-link>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import { Datetime } from 'vue-datetime'
    import Multiselect from 'vue-multiselect'
    export default {
        name: 'create-trainer',
        components: {
            'datetime': Datetime,
            Multiselect
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        data (){
            return {
                profile: {},
                file_name: '',
                files: {},
                is_loading: false,
                specialize: null,
                selected_location : null,
                location_options: [],
                form: {
                    category: 'personal trainer',
                    firstName: '',
                    lastName: '',
                    price: '',
                    rewardPoints: '',
                    about: '',
                    specialization: [],
                    duration: '',
                    locationID : ''
                },
                isLoading: false,
                value: [],
                options: [],
                load_specialization: false,
            }
        },
        mounted(){
            this.getAuthUser()
            this.getSpecializeList()
            this.getListLocation()
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                        }, response => {})
                    } else{
                        this.profile = this.user
                    }
                }
            },
            doCreate(){
                this.is_loading = true
                
                const formData = new FormData()
                if (this.form.category === 'personal trainer') {
                     if(this.selected_location == null){
                    this.$toastr('error', 'Location required')
                    return
                    }
                this.form.locationID = this.selected_location.id
                formData.append('duration', this.form.duration)
                formData.append('price', parseInt(this.form.price.toString().replace(/\./g,'')))
                formData.append('rewardPoints', this.form.rewardPoints)
                formData.append('about', this.form.about)
                formData.append('specializationId', `[${this.form.specialization.map(e => e.id)}]`)
                formData.append('image', this.files)
                 formData.append('locationID', this.form.locationID)
                }

                formData.append('firstName', this.form.firstName)
                formData.append('lastName', this.form.lastName)
                formData.append('category', this.form.category)
             
                this.$http.post(process.env.API_URL + 'teacher', formData).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.$router.push({ name: 'trainer', params: {} })
                        this.$toastr('success', response.data.message)
                    }
                }, response => {})
            },
            formatPrice(value){
                this.form.price = this.formatNumber(value)
            },
            formatNumber(value){
                let temp = value.toString().replace(/\./g,'')
                let result = temp.replace(/[^0-9,]/g, "").replace(/(\d)(?=(\d\d\d)+(?!\d))/g, "$1.")
                return result
            },
            onChangeFile(event, attribute, defaultValue) {
                let files = event.target.files || event.dataTransfer.files;
                const MAX_SIZE_FILE = 2*1024*1024
                if (files[0].size > MAX_SIZE_FILE) {
                  this.$toastr('error', 'file size max 2Mb')
                  return
                }
                this.files = files[0]
                this.file_name = files[0].name
            },
             getListLocation(query = null){
                this.$http.get(process.env.API_URL + 'location/all',{params: {'keyword': query}}).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.location_options = response.data.data
                        var response = response.data.data
                        // var array1 = [{id:'', location_name: 'All'}]
                        // this.filter_location_options = [...array1,...response]
                        
                    }
                }, response => {})
            },
            getSpecializeList(query) {
              this.load_specialization = true
                this.$http.get(process.env.API_URL + 'teacher-specialization/teacher-all', {params: {'keyword': query}}).then(response => {
                    this.load_specialization = false
                    var status = response.data.status
                    if(status){
                        this.options = response.data.data
                    }
                }, response => {})
            }
        }
    }
</script>
<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
<style lang="css" scoped>
    @media (max-width: 375px) {
        .btn.small{
            min-width: 100px
        }
    }
</style>



