import Vue from 'vue'

// Number, Decimal & Currency Format
var numeral = require("numeral")
numeral.locale('id')

numeral.register('locale', 'id', {
    delimiters: {
        thousands: '.',
        decimal: ','
    },
    abbreviations: {
        thousand: 'k',
        million: 'm',
        billion: 'b',
        trillion: 't' },
    ordinal : function (number) {
        return number === 1 ? 'er' : 'me'
    },
    currency: {
        symbol: 'Rp'
    }
})

Vue.filter("numberFormat", function (value) {
    return numeral(value).format("0,0")
})

Vue.filter("decimalFormat", function (value) {
    return numeral(value).format("0,0.000")
})

Vue.filter("currencyFormat", function (value) {
    return numeral(value).format("$0,0.")
})

Vue.filter("currencyFormatDecimal", function (value) {
    return numeral(value).format("$0,0.00")
})

// Time & Date Format
var moment = require('moment')
// moment.locale('id')

Vue.filter("timeFormatDate", function (date) {
    return moment(date).format('HH:mm')
})

Vue.filter("timeFormatTime", function (time) {
    return moment(time, 'HH:mm:ss').format('HH:mm')
})

Vue.filter("dateFormat", function (date) {
    return moment(date).format('DD-MM-YYYY')
})

Vue.filter("dateFormatFull", function (date) {
    return moment(date).format('DD MMMM YYYY')
})

Vue.filter("dateDay", function (date) {
    return moment(date).format('dddd <br> DD-MM-YYYY')
})

Vue.filter("dateDay2", function (date) {
  return moment(date).format('dddd, DD-MM-YYYY')
})

Vue.filter("dateTimeFormat", function (date) {
    return moment(date).format('DD MMM YYYY HH:mm')
})

Vue.filter("dateTimeFormatFull", function (date) {
    return moment(date).format('DD MMMM YYYY HH:mm')
})

Vue.filter("getCurrentYear", function () {
    return moment().format('YYYY')
})

Vue.filter("scheduleDay", function (date) {
    return moment(date, 'DD-MM-YYYY').format('dd')
})
Vue.filter("scheduleDay1", function (date) {
    return moment(date, 'DD-MM-YYYY').format('ddd')
})

Vue.filter("scheduleDate", function (date) {
    return moment(date, 'DD-MM-YYYY').format('D')
})
Vue.filter("scheduleDate1", function (date) {
    return moment(date, 'DD-MM-YYYY').format('D-M')
})

Vue.filter("scheduleFullDate", function (date) {
  return moment(date, 'DD-MM-YYYY').format('D-M-YY')
})

Vue.filter("scheduleFullDate1", function (date) {
  return moment(date, 'DD-MM-YYYY').format('D-M-YYYY')
})


