<script>
    import { mapState } from 'vuex';

    export default {
        name: 'layout',
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        watch: {
            '$mq.resize': 'countContentWrapperHeight',
        },
        data(){
            return {
                state: 'dashboard',
                profile: {},
                current_date: '00-00-0000',
                height:window.innerHeight,
                role: ""
            }
        },
        mounted(){
            const user = localStorage.getItem("user")
            this.role =  JSON.parse(user)
            this.getAuthUser()
            this.countContentWrapperHeight()
            this.getCurrentRouter()
           
            var moment = require('moment')
            var date = moment().format('YYYY-MM-DD').toString()
            this.current_date = moment(date, 'YYYY-MM-DD').format('DD-MM-YYYY')
            
        },
        updated(){
            this.countContentWrapperHeight()
            this.getCurrentRouter()
        },
        methods: {
            countContentWrapperHeight(){
                var vHeight = window.innerHeight
                var sidebar = document.querySelector(".sidebar")
                var contentWrapper = document.querySelector(".content-wrapper")

                if(sidebar && sidebar.clientHeight > vHeight){
                    if(contentWrapper.length > 0){
                        contentWrapper[0].style.minHeight  = (sidebar.clientHeight + 50) + "px"
                    }
                } else {
                    if(contentWrapper && contentWrapper.length > 0){
                        contentWrapper[0].style.minHeight  = "100vh"
                    }
                }
            },
            getCurrentRouter(){
                var router = this.$router.currentRoute.path
              
                if(router.indexOf('dashboard') >= 0){
                    this.state = 'dashboard'
                } else if (router.indexOf('report') >= 0){
                    this.state = 'report'
                }else if (router.indexOf('user') >= 0){
                    this.state = 'user'
                } else if (router.indexOf('transaction') >= 0){
                    this.state = 'transaction'
                }  else if (router.indexOf('class') >= 0){
                    this.state = 'class'
                }  else if (router.indexOf('package') >= 0){
                    this.state = 'package'
                }else if (router.indexOf('discountcode') >= 0){
                    this.state = 'discountcode'
                } else if (router.indexOf('promo') >= 0){
                    this.state = 'promotion'
                } else if (router.indexOf('schedule') >= 0){
                    this.state = 'schedule'
                } else if (router.indexOf('merchandise') >= 0){
                    this.state = 'merchandise'
                } else if (router.indexOf('banner') >= 0){
                    this.state = 'banner'
                } else if (router.indexOf('activityrole') >= 0){
                    this.state = 'activityrole'
                } else if (router.indexOf('sales') >= 0){
                    this.state = 'sales'
                } else if (router.indexOf('bookings') >= 0){
                    this.state = 'bookings'
                } else if (router.indexOf('voucher') >= 0){
                    this.state = 'reward'
                } else if (router.indexOf('achievement') >= 0){
                    this.state = 'achievement'
                } else if (router.indexOf('trainer') >= 0){
                    this.state = 'trainer'
                } else if (router.indexOf('location') >= 0){
                    this.state = 'location'
                } else if (router.indexOf('specialization') >= 0){
                    this.state = 'specialization'
                } else if (router.indexOf('credit-price') >= 0){
                    this.state = 'credit'
                } else if (router.indexOf('sport-type') >= 0){
                    this.state = 'sport-type'
                } else if (router.indexOf('wellness') >= 0){
                    this.state = 'wellness'
                } else if (router.indexOf('recovery') >= 0){
                    this.state = 'recovery'
                } else if (router.indexOf('customer-feedback') >= 0){
                    this.state = 'customer-feedback'
                } else if (router.indexOf('terms-and-conditions') >= 0){
                    this.state = 'terms-and-conditions'
                }
            },
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                         
                            this.role = response.data.data
                        }, response => {})
                    } else{
                        this.profile = this.user
                       
                    }
                }
            },
            doLogout(){
                this.$store.dispatch('logout', {requestOptions: {method: 'GET'}}).then(() => {
                    this.$router.push({name: 'root', params: ''})
                });
            },
            
        },
    }
</script>

