<template lang="html">
    <transition name="modal">
        <div class="modal-mask">
            <div class="modal-wrapper">
                <div class="modal-container">
                    <div class="modal-header mb-3">
                        <slot name="header">
                            default header
                        </slot>
                        <div class="modal-close" @click="$emit('close')">
                            <img src="~@/assets/images/ic_close.svg" alt="">
                        </div>
                    </div>
                    <slot name="body"> __________________ </slot>
                    <!-- <form @submit.prevent="doConfirm()">
                        <div class="modal-body">
                            <slot name="body">
                                default body
                            </slot>
                        </div>

                        <div class="modal-footer">
                            <button type="submit" class="btn btn-main small" :class="{'disabled': is_confirm}">
                                <slot name="agree-button">
                                    ADD
                                </slot>
                                <span class="btn-loading" v-if="is_confirm"><i class="fa fa-spinner fa-spin fa-fw"></i></span>
                            </button>
                            <button type="button" class="btn btn-main inverse small" @click="doClose()">
                                <slot name="cancel-button">
                                    CANCEL
                                </slot>
                            </button>
                        </div>
                    </form> -->
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
    import { mixin as onClickOutside } from 'vue-on-click-outside'

    export default {
        name: 'confirmation',
        mixins: [onClickOutside],
        data () {
            return {
                is_confirm: false,
            }
        },
        mounted(){},
        methods:{
            doConfirm(){
                this.is_confirm = true
                this.$emit('confirm')
            },
            doClose(){
                this.is_confirm = false
                this.$emit('close')
            },
        },
    }
</script>

<style lang="css" scoped>
    .btn-main{
        min-width: 100px;
    }
    /*-----------------------------------------------------------------------------------*/
    /* Modal */
    /*-----------------------------------------------------------------------------------*/
    .modal-mask {
        position: fixed;
        z-index: 9999;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, .5);
        display: block;
        transition: all .3s ease;
        text-align: left;
        overflow-x: hidden;
        overflow-y: auto;
    }
    .modal-mask::-webkit-scrollbar {
        width: 8px;
        height: 8px;
    }
    .modal-mask::-webkit-scrollbar-track {
        box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    }
    .modal-mask::-webkit-scrollbar-thumb {
        background-color: #26732d;
        outline: 1px solid slategrey;
        -webkit-border-radius: 5px;
        border-radius: 5px;
    }
    .modal-wrapper {
        display: flex;
        width: 50%;
        margin: -30px auto;
        align-items: center;
        height: 100%;
    }
    .modal-container {
        width: 100%;
        padding: 30px 30px 30px 30px;
        background-color: #fff;
        border-radius: 10px;
        box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
        transition: all .3s ease;
        position: relative;
        text-align: center;
    }
    .modal-header {
        position: relative;
        justify-content: center;
        padding: 5px;
        color: #ef3b29;
        font-family: "gothambold";
        font-size: 20px;
        letter-spacing: 1.43px;
        border-bottom: none;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
    }
    .modal-close{
        position: absolute;
        /* top: -16px;
        right: -17px; */
        top: -22px;
        right: -14px;
        cursor: pointer;
    }
    .modal-close svg{
        width: 14px;
    }
    .modal-close svg .st0{
        fill: none;
        stroke: #aaaaaa;
        stroke-width: 2;
        stroke-linecap: round;
        stroke-linejoin: round;
        stroke-miterlimit: 10;
    }
    .modal-close:hover svg .st0{
        stroke: gray;
    }
    .modal-mask /deep/ .modal-body {
        padding: 10px 60px 10px 60px;
        font-size: 14px;
        color: #606060;
        /* font-family: 'gothammedium'; */
        text-align: left;
    }
    .modal-mask /deep/ .modal-body::-webkit-scrollbar {
        width: 8px;
        height: 8px;
    }
    .modal-mask /deep/ .modal-body::-webkit-scrollbar-track {
        box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    }
    .modal-mask /deep/ .modal-body::-webkit-scrollbar-thumb {
        background-color: #02b001;
        outline: 1px solid slategrey;
        -webkit-border-radius: 5px;
        border-radius: 5px;
    }
    .modal-mask /deep/ .modal-footer {
        padding: 5px;
        border-top: none;
        justify-content: center;
    }
    .modal-enter {
        opacity: 0;
    }
    .modal-leave-active {
        opacity: 0;
    }
    .modal-enter .modal-container,
    .modal-leave-active .modal-container {
        -webkit-transform: scale(1.1);
        transform: scale(1.1);
    }
    .btn-green-outline.btn-xs{
        padding: 5px 18px;
        font-size: 14px;
    }
    @media (max-width: 991px) {
        .modal-wrapper {
            width: 50%;
        }
    }
    @media (max-width: 767px){
        .btn-green-outline {
            width: auto;
        }
        .modal-body {
            padding: 10px 5px 15px 5px;
        }
    }
    @media (min-width: 481px) and (max-width: 767px) {
        .modal-wrapper {
            width: 85% !important;
        }
    }
    @media (max-width: 480px) {
        .modal-wrapper {
            width: 90% !important;
        }
    }
</style>



