<template>
    <div>
        <div class="mb-4">
            <label class="page-header">PACKAGE</label>
            <label class="breadcrumb">Package List/Package Detail</label>
        </div>
        <label class="page-title mr-4">PACKAGE DETAIL</label>
 

        <div class="detail-profile mt-4">
            <div class="row mb-4">
                <div class="col-12 col-md-12 col-lg-5 mb-3 text-center">
                    <template v-if="!is_loading">
                        <img :src="data.imageUrl" :alt="data.image" class="img-fluid mb-4">
                    </template>
                    <i class="fa fa-spinner fa-pulse fa-2x fa-fw" v-else></i>
                </div>
                <div class="col-12 col-sm-6 col-md-4 col-lg-4">
                    <div class="mb-4">
                        <label>PACKAGE TITLE</label>
                        <div value>{{data.name}} </div>
                    </div>
                   
                    <div class="row">
                     
                        <div class="col-12 col-sm-10 col-md-10 col-lg-6 mb-4">
                            <label>PRICE TYPE</label>
                            <div value v-if="data.price_type === 'credit'">CREDIT</div>
                            <div value v-else-if="data.price_type === 'money'">MONEY</div>
                            <div v-else>---</div>
                        </div>
                      
                    </div>

                   
                    <div class="row">
                        <div class="col-12 col-sm-10 col-md-10 col-lg-6 mb-4">
                            <label>PRICE</label>
                            <div value v-if="data.price_type == 'credit'"> {{data.price | numberFormat}} CREDIT</div>
                            <div value v-else-if="data.price_type === 'money'">Rp {{ data.price | numberFormat }}</div>
                            <div v-else>---</div>
                        </div>

                        <div class="col-12 col-sm-10 col-md-10 col-lg-6 mb-4">
                            <label>ACTIVE PERIOD</label>
                            <div value v-if="data.expiry  >= 0">{{ data.expiry | numberFormat }}</div>
                            <div v-else>---</div>
                        </div>   
                        </div>
                    <div class="row">
                        <div class="col-12 col-sm-10 col-md-10 col-lg-6 mb-4">
                            <label>FIRST TIME</label>
                            <div value v-if="data.firstTimeOnly == 0"> NO</div>
                            <div value v-else-if="data.firstTimeOnly === 1">YES</div>
                        <div v-else>---</div>
                    </div>
                        <div class="col-12 col-sm-10 col-md-10 col-lg-6 mb-4">
                            <label>MONTH CLASS</label>
                            <div value v-if="data.month_class_package == 0"> NO</div>
                            <div value v-else-if="data.month_class_package === 1">YES</div>
                            <div v-else>---</div>
                    </div>   
                    </div>

                    <div class="row">
                        <div class="col-12 col-sm-10 col-md-10 col-lg-6 mb-4">
                            <label>IS FREE CLASS</label>
                            <div value v-if="data.is_free_class == 0"> NO</div>
                            <div value v-else-if="data.is_free_class === 1">YES</div>
                            <div v-else>---</div>
                        </div>
                        <div class="col-12 col-sm-10 col-md-10 col-lg-6 mb-4">
                            <label>IS FREE RECOVERY</label>          
                            <div value v-if="data.is_free_recovery == 0"> NO</div>
                            <div value v-else-if="data.is_free_recovery === 1">YES</div>
                            <div v-else>---</div>
                        </div>
                    </div>
                
                    <div class="mb-4">
                        <label>DESCRIPTION</label>
                        <div value v-if="data.description">{{ data.description }}</div>
                        <div v-else>---</div>
                    </div>
            
                     <div class="mb-4">
                        <label>PACKAGE POLICY</label>
                        <div value v-if="data.package_policy">{{ data.package_policy }}</div>
                        <div v-else>---</div>
                    </div>
                </div>
            </div>
        </div>

        <confirmation v-if="delete_confirmation" @close="delete_confirmation = false" @confirm="deletePackage()">
            <div slot="header">DELETE USER</div>
            <div slot="body">
                are you sure you want to delete this user? this action can not be reverted
            </div>
        </confirmation>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import Confirmation from '@/components/Confirmation'

    export default {
        name: 'detail-trainer',
        props: ['id'],
        components: {
            'confirmation': Confirmation
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        data(){
            return{
                profile: {},

                is_loading: false,
                is_member: false,
                is_can_edit: false,
                delete_confirmation: false,
                data: {
                    firstName: 'John',
                    lastName: 'Doe',
                    price: '1000000',
                    rewardPoints: '100',
                    type: 'Pt',
                },
            }
        },
        mounted(){
            this.getAuthUser()
            this.getDetailUser()
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                        }, response => {})
                    } else{
                        this.profile = this.user
                    }
                }
            },
            getDetailUser(){
                this.is_loading = true
                this.$http.get(process.env.API_URL + 'package/' + this.id).then(response => {
                    this.is_loading = false
                    if(response.status == 404){
                        this.$toastr('error', response.statusText)
                        this.$router.push({ name: 'package', params: {} })
                    } else if(response.status == 200){
                        var status = response.data.status
                        if(status){
                            this.data = response.data.data
                        }
                    }
                }, response => {})
            },
            deletePackage(){
                this.$http.post(process.env.API_URL + 'package/delete/' + this.id).then(response => {
                    this.delete_confirmation = false
                    var status = response.data.status
                    if(status){
                        this.$toastr('success', response.data.message)
                        this.$router.push({ name: 'package' })
                    }
                }, response => {})
            },
        }
    }
</script>

<style lang="css" scoped>
    .detail-profile{
        text-align: left;
    }
    .detail-profile label{
        margin-bottom: .1rem;
        font-size: 12px;
        letter-spacing: .75px;
    }
    .detail-profile [value]{
        font-size: 14px;
        font-family: 'gothammedium';
    }
    @media (max-width: 576px) {
        .detail-profile{
            text-align: center;
        }
    }

    @media (max-width: 480px) {
        [action-button]{
            display: block;
        }
    }

    .tab ul{
        padding-left: 0;
        list-style: none;
    }
    .tab ul li{
        display: inline-block;
    }
    .tab ul li{
        padding: 5px 15px;
    }
    .tab ul li a{
        color: #ef3b29;
    }
    .tab ul li.active{
        background-color: #ef3b29;
        border-radius: 25px;
        padding: 5px 15px;
    }
    .tab ul li.active a{
        color: #fff;
    }
    .tab ul li a:not(.active):hover{
        opacity: .7;
    }
    .tab ul li a.active:hover{
        box-shadow: 0 0 3px 1px #ef3b29;
    }
</style>



