<template>
    <div>
        <div class="mb-4">
            <label class="page-header">CLASS</label>
            <label class="breadcrumb">Class List/Class Detail</label>
        </div>

        <label class="page-title mr-4">CLASS DETAIL</label>
        <span action-button>
            <router-link :to="{ name: 'edit_class', params: {id: id} }" class="btn btn-main inverse small mr-2 mb-1" v-if="!profile.isOperator">Edit Class</router-link>
            <button type="button" class="btn btn-main inverse small mb-1" @click="delete_confirmation = true" v-if="!profile.isOperator">Delete Class</button>
        </span>

        <div class="row mt-4">
            <div class="col-12 col-lg-11">
                <div class="row">
                    <div class="col-12 col-md-12 col-lg-5 mb-3 text-center">
                        <template v-if="!is_loading">
                            <img v-for="image in data.sportsClassImages" v-bind:key="image.id" v-bind:src="image.imageUrl" v-bind:alt="image.name" class="img-fluid mb-4">
                        </template>
                        <i class="fa fa-spinner fa-pulse fa-2x fa-fw" v-else></i>
                    </div>
                    <div class="col-12 col-md-12 col-lg-7">
                        <h5 class="card-title">{{data.name}}</h5>
                        <div class="row">
                          
                            <div class="col-12 col-sm-10 col-md-10 col-lg-6">
                                <label class="title">Description</label>
                                <p>
                                    <span v-if="is_loading"><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                    <span v-else>{{data.description}}</span>
                                </p>
                            </div>
                            <div class="col-6 col-lg-3">
                                <label class="title">Duration</label>
                                <p>
                                    <span v-if="is_loading"><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                    <span v-else>{{ data.duration | numberFormat }} minutes </span>
                                </p>
                            </div>
                     
                        </div>
                        <div class="row" v-if="data.sportsClassAsset">
                            <div class="col-12 col-sm-10 col-md-10 col-lg-6">
                                <label class="title">Cancellation Policy</label>
                                <p>
                                    <span v-if="is_loading"><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                    <span v-else>{{data.cancellationPolicy}}</span>
                                </p>
                            </div>
                            <div class="col-6 col-lg-3">
                                <label class="title">Loyalty Points</label>
                                <p>
                                    <span v-if="is_loading"><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                    <span v-else>{{ data.rewardPoints | numberFormat }} pts </span>
                                </p>
                            </div>
                            </div>
                      <div class="row">
                            <div class="col-12 col-sm-10 col-md-10 col-lg-6">
                                <label class="title">Prepare</label>
                                <p>
                                    <span v-if="is_loading"><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                    <span v-else>{{data.prepare}}</span>
                                </p>
                            </div>
                             <div class="col-6 col-lg-3">
                                <label class="title">Price</label>
                                <p>
                                    <span v-if="is_loading"><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                    <span v-else>{{data.price}} Credit</span>
                                </p>
                            </div>
                      </div>
                       <div class="row">
                            <div class="col-12 col-sm-10 col-md-10 col-lg-6">
                                <label class="title">Is Active</label>
                                <p>
                                    <span v-if="is_loading"><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                    <span v-else>{{ data.isActive === 0 ? "No" : "Yes" }}</span>
                                </p>
                            </div>
                            
                      </div>
                    
                                <img v-bind:src="data.sportsClassAsset.logoUrl" class="img-thumbnail" style="width: 100px;">
                            
                      
                        
                    </div>
                </div>
            </div>
        </div>

        <confirmation v-if="delete_confirmation && !profile.isOperator" @close="delete_confirmation = false" @confirm="deleteClass()">
            <div slot="header">DELETE CLASS</div>
            <div slot="body">
                are you sure you want to delete this class? this action can not be reverted
            </div>
        </confirmation>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import Confirmation from '@/components/Confirmation'

    export default {
        name: 'detail-user',
        props: ['id'],
        components: {
            'confirmation': Confirmation
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
         watch: {
         
            delete_confirmation: function() {
            if(this.delete_confirmation){
                document.documentElement.style.overflow = 'hidden'
            return
            }
            document.documentElement.style.overflow = 'auto'
            }
        },
        data(){
            return{
                IMAGE_URL: process.env.IMAGE_URL,
                profile: {},
                is_loading: false,
                delete_confirmation: false,
                data: {
                    id: null,
                    branchID: null,
                    description: '',
                    duration: '',
                    image: '',
                    imageUrl: '',
                    name: '',
                    quota: null,
                    color: '',
                    rewardPoints: '',
                    loyaltyPoints: '',
                    sportsClassAsset: {}
                },
                data_image: '',
            }
        },
        mounted(){
            this.getAuthUser()
            this.getDetailClass()
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                        }, response => {})
                    } else{
                        this.profile = this.user
                    }
                }
            },
            getDetailClass(){
                this.is_loading = true
                this.$http.get(process.env.API_URL + 'sports-class/' + this.id).then(response => {
                    this.is_loading = false
                    if(response.status == 404){
                        this.$toastr('error', response.statusText)
                        this.$router.push({ name: 'class', params: {} })
                    } else if(response.status == 200){
                        var status = response.data.status
                        if(status){
                            this.data = response.data.data
                            this.data_image = this.IMAGE_URL + this.data.imageUrl
                        }
                    }
                }, response => {})
            },
            deleteClass(){
             
                this.$http.post(process.env.API_URL + 'sports-class/delete/' + this.id).then(response => {
                    this.delete_confirmation = false
                    document.documentElement.style.overflow = 'auto'
                    var status = response.data.status
                    if(status){
                      
                        this.$toastr('success', response.data.message)
                        this.$router.push({ name: 'class' })
                    }
                }, response => {})
            },
        }
    }
</script>

<style lang="css" scoped>
    label.title{
        font-size: 14px;
        color: #ef3b29;
        font-family: 'gothambold';
    }
    p{
        color: #000;
    }

    @media (max-width: 480px) {
        [action-button]{
            display: block;
        }
    }
    img[src=""], img:not([src]) { display: none; }
</style>



