<template>
  <div class="">
    <div class="mb-4">
      <label class="page-header">REPORT CLASS</label>
      <label class="breadcrumb">Report Class List/Report Detail</label>
    </div>
  
    <div class="row">
      <div class="col-12">
        <div class="table-responsive">
          <table class="table">
            <thead>
              <tr>
                <th>CATEGORY</th>
                <th>CLASS NAME</th>
                <th>PRICE</th>
                 <th>TOTAL CREDIT</th>
                <!-- <th style="width: 14%;">SCHEDULE ID</th> -->
                <th>USER LIST</th>   
                <th>DATE</th>          
              </tr>
            </thead>
            <tbody v-if="is_loading">
              <tr>
                <td colspan="7" class="text-center">
                  <span
                    ><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i
                  ></span>
                </td>
              </tr>
            </tbody>
            <tbody v-else>
                <tr class="text-center" v-if="data.length <= 0">
                <td colspan="7">
                  <span>No matching records found</span>
                </td>
              </tr>
              <tr v-for="(item, index) in data" v-else>
                <td>
                  <span>{{ item.category }}</span>
                </td>
                <td>
                  <span>{{ item.name }} </span>
                </td>
                 <td>
                  <span v-if="item.price_credit">{{item.price_credit}} CREDIT</span>
                  <span v-else>-</span>
                </td>
                 <td>
                  <span v-if="item.price_credit_total">{{item.price_credit_total}}</span>
                   <span v-else>-</span>
                </td>
                 <!-- <td>
                 <span>{{ item.sessionID }}</span>
                </td> -->
                <td>
                  <span v-if="item.user_count">
                    <a href="" class="link" @click.prevent="showUsedList(item)"> {{ item.user_count }} </a>
                   </span>
                    <span v-else>-</span>
                </td>
                 <td>
                 {{item.start | dateTimeFormat}}
                </td>
              </tr>
              
            </tbody>
          </table>
        </div>

        <div class="">
          <nav
            aria-label="Page navigation"
            class="page navigation"
            v-if="last_page > 1"
          >
            <paginate
              :page-count="last_page"
              :click-handler="callbackPaginate"
              :prev-text="'<img src=' + arrow + ' width=' + 17 + '>'"
              :next-text="'<img src=' + arrow + ' width=' + 17 + '>'"
              :container-class="'pagination'"
              :page-class="'page-item'"
              :page-link-class="'page-link'"
              :prev-class="'page-item'"
              :prev-link-class="'page-link'"
              :next-class="'page-item'"
              :next-link-class="'page-link'"
              :page-range="5"
              :margin-pages="4"
              ref="paginate"
            >
            </paginate>
          </nav>
        </div>
      </div>
    </div>
   <div class="" used-list>
            <used-list v-if="show_modal" @close="doCloseModal()">
                <div slot="header">USER LISTS</div>
                <div slot="body">
            <div class="row justify-content-between align-items-center">
            <div class="col-12 col-sm-6 col-md-5 col-lg-4 col-xl-3">
               
            </div>

            </div>
                    <div class="table-responsive">
                        <table class="table">
                            <thead>
                                <th>FULL NAME</th>
                                <th>CREDIT</th>
                                <th>TOTAL PRICE</th>
                                <th>PHONE NUMBER</th>
                                <th>MEMBER ID</th>
                                <th>GENDER</th>
                                <th>EMAIL</th>
                                <th>DATE OF BIRTHDAY</th>
                             
                            </thead>
                            <tbody v-if="is_view_used_list">
                                <tr>
                                    <td colspan="6" class="text-center">
                                        <span ><i class="fa fa-spinner fa-pulse fa-fw"></i></span>
                                    </td>
                                </tr>
                            </tbody>
                            <tbody  v-else>
                                <tr class="text-center" v-if="used_list.length <= 0">
                                    <td colspan="6">
                                        <span >No matching records found</span>
                                    </td>
                                </tr>
                                <tr v-for="item in used_list" :key="item.order_code" v-else>
                                    <td>
                                         {{item.name}}
                                    </td>
                                        <td> <span v-if="item.price_used">{{ item.price_used }} </span>
                                            <span v-else>-</span>
                                       </td>
                                    <td> {{ item.price_used_value | currencyFormat}} 
                                    </td>
                                    <td> {{ item.phone }} </td>
                                    <td> {{ item.memberID }} </td>
                                    <td> 
                                      <span v-if="item.gender">{{ item.gender }} 
                                  
                                      </span>
                                    <span v-else>-</span>
                                     </td>
                                    <td> {{ item.email }}</td>
                                    <td> {{ item.dateOfBirth | dateFormat }}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                     <div class="">
                    <nav aria-label="Page navigation" class="page navigation" v-if="last_page_used > 1">
                        <paginate
                            :page-count="last_page_used"
                            :click-handler="callbackPaginateUserList"
                            :prev-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                            :next-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                            :container-class="'pagination'"
                            :page-class="'page-item'"
                            :page-link-class="'page-link'"
                            :prev-class="'page-item'"
                            :prev-link-class="'page-link'"
                            :next-class="'page-item'"
                            :next-link-class="'page-link'"
                            :page-range="5"
                            :margin-pages="4"
                            ref="paginate">
                        </paginate>
                    </nav>
                </div>
                </div>
            </used-list>
        </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import Paginate from "vuejs-paginate";
import debounce from "debounce";
import arrow from "@/assets/images/ic_arrow_square_hover.svg";
import Confirmation from "@/components/Confirmation";
import ConfirmationUpdate from "@/components/ConfirmationUpdate";
 import Multiselect from 'vue-multiselect'
 import Modal from '@/components/Modal'

export default {
  name: 'detail-recovery',
  props: ['id', 'category'],
  components: {
    Multiselect,
    paginate: Paginate,
    confirmation: Confirmation,
    confirmationupdate: ConfirmationUpdate,
     'used-list': Modal
  },
  computed: mapState(["user", "isAuthenticated"]),
  watch: {
    show_modal: function() {
    if(this.show_modal){
      document.documentElement.style.overflow = 'hidden'
      return
    }
      document.documentElement.style.overflow = 'auto'
    }
  },
  data() {
    return {
      profile: {},
      keyword: "",
      sort_column: "email", 
      sort_type: "asc",
      is_loading: false,
      data: [],
      selected_category: { id: "", name:"All" },
      category_options: [{ id: "", name:"All" },{id: "class", name: "Class"}, { id: "personal trainer",name: "Personal Trainer"} , { id: "wellness",name: "Wellness"},{ id: "recovery",name: "Recovery"}],
      last_page: 0,
      current_page: 0,
      delete_confirmation: false,
      update_confirmation: false,
      data_temp: {},
      index_temp: "",
      arrow: arrow,
       show_modal: false,
      is_view_used_list : false,
      last_page_used: 0,
      current_page_used: 0,
    };
  },
  mounted() {
    this.getAuthUser();
    this.getListReport()

  },
  methods: {
    getAuthUser() {
      if (this.isAuthenticated) {
        if (typeof this.user.id == typeof undefined || !this.user.id) {
          this.$http.get(process.env.API_URL + "user/profile").then(
            response => {
              this.profile = response.data.data;
            },
            response => {}
          );
        } else {
          this.profile = this.user;
        }
      }
    },
    callbackPaginate: function(pageNum) {
      this.getListReport(true, pageNum, this.sort_column, this.sort_type);
    },
      callbackPaginateUserList: function(pageNum) {
                this.showUsedList(null, pageNum)
            },
    search: debounce(function() {
      this.getListReport(false);
    }, 250),

     getListReport(loading, page = null){
      this.is_loading = loading;
        this.$http.get(process.env.API_URL + 'report/class_detail', {params: { category: this.category, date: '', id: this.id,   page: page, limit :10
        }}).then(response => {
          this.is_loading = false;
        var status = response.data.status
        if(status){
          this.data = response.data.data.data;
              this.last_page = response.data.data.last_page;
              this.current_page = response.data.data.current_page;
         }
      }, response => {})
    },
  
     onChangeFilterClass(item, index){
                this.getListReport()
    },
    showDeleteConfirmation(data, index) {
      this.data_temp = data;
      this.index_temp = index;
      this.delete_confirmation = true;
    },
      showUsedList(item, page = null){
                this.show_modal = true
                this.is_view_used_list = true
                this.data_temp = item === null ? this.data_temp : item
                this.$http.get(process.env.API_URL + 'report/class_detail_user/', {params: {id: this.data_temp.id,'category': this.data_temp.category,sessionID: this.data_temp.sessionID, 'page': page, 'limit' : 10}}).then(response => {
                    this.is_view_used_list = false
                    var status = response.data.status
                    if(status){
                        this.used_list = response.data.data.data
                        this.last_page_used = response.data.data.last_page
                        this.current_page_used = response.data.data.current_page
                    }
                }, response => {})
            },
    doCloseModal(){
                this.show_modal = false
                this.data_temp  = {}
               
    },
  }
};
</script>

<style lang="css" scoped>
 .table th, .table td{
        font-size: 11px;
    }
.pointer {
  cursor: pointer;
  color: #00A6ED;
}
.class-filter{
  width: 750px;
  margin-bottom: 1.5rem;
}
.class-filter .form-check{
  display: inline-block;
  width: 30%;
  margin-bottom: .4rem;
}

@media (max-width: 1199px) {
  .board-report .col{
    width: 24%;
  }
}
@media (max-width: 991px) {
      
.class-filter{
  width: 100%;
 }
.class-filter .form-check{
  width: 38%;
 }
}
@media (max-width: 576px) {
.class-filter .form-check{
  width: 90%;
  }
}
[used-list] /deep/ .modal-wrapper{
        width: 95%;
      
    }
</style>



