<template>
  <div class="">
    <div class="mb-4">
      <label class="page-header">TRANSACTION</label>
      <label class="breadcrumb">Transaction List</label>
    </div>
      <div class="col-12 col-sm-6 col-md-5 col-lg-4 col-xl-3 mb-3 no-padding-left">
            <div class="form-group mb-45 ">
                <label class="mb-2">PAYMENT STATUS</label>
                  <multiselect v-model="selected_status" :options="status_options" :searchable="true"    :hide-selected="true" :show-no-results="true" track-by="id" label="name" :close-on-select="true" :show-labels="false" placeholder="SELECT STATUS"  @select="getTransactionList"></multiselect>
            </div>
      </div>
  
    <div class="row">
      <div class="col-12">
        <div class="table-responsive">
          <table class="table">
            <thead>
              <tr>
                <th>TRANSACTION CODE</th>
                <th>MEMBER</th>
                <th>TRANSACTION DATE</th>
                <th>PACKAGE</th>
                <th style="width: 14%;">TOTAL CREDIT</th>
                <th>PRICE</th>
                <th>DISCOUNT</th>     
                <th>DISCOUNTCODE</th>     
                <th>TOTAL</th>  
                <th>PAYMENT TYPE</th>       
                <th>STATUS</th>            
              </tr>
            </thead>
            <tbody v-if="is_loading">
              <tr>
                <td colspan="7" class="text-center">
                  <span
                    ><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i
                  ></span>
                </td>
              </tr>
            </tbody>
            <tbody v-else>
               <tr class="text-center" v-if="data.length <= 0">
                <td colspan="7">
                  <span>No matching records found</span>
                </td>
              </tr>
              <tr v-for="(item, index) in data" :key="index" v-else>
                 <td>
                  <span>{{ item.code }}</span>
                </td>
                 <td>
                  <span>{{ item.fullName }}</span>
                </td>
                <td>
                  <span>{{ item.transactionDate }}</span>
                </td>
                <td v-if="item.packageOrders.length > 0"> 
                                <div v-for="packet in item.packageOrders" :key="packet.id"> 
                                    {{packet.package.name}}   </div>    
              </td>
                <td v-else> 
                                <div v-for="credit in item.creditOrders" :key="credit.id"> 
                                    CREDIT </div>    
                </td>
                 <td v-if="item.packageOrders.length > 0"> 
                                <div v-for="packet in item.packageOrders" :key="packet.id"> 
                                    {{packet.package.credit}}   </div>    
              </td>
                <td v-else> 
                                <div v-for="credit in item.creditOrders" :key="credit.id"> 
                                    {{credit.credit}}  </div>    
                </td>
                  <td v-if="item.packageOrders.length > 0"> 
                                <div v-for="packet in item.packageOrders" :key="packet.id"> 
                                    {{packet.package.price | currencyFormat }}  </div>    
              </td>
                <td v-else> 
                                <div v-for="credit in item.creditOrders" :key="credit.id"> 
                                    {{credit.price | currencyFormat}}  </div>    
                </td>
                 <td>
                  <span>{{ item.totalDiscount | currencyFormat }}</span>
                </td>
                 <td>
                 <span>{{ item.promoCode }}</span>
                </td>
                 <td>
                 <span>{{ item.total | currencyFormat }}</span>
                </td>
                  <td>
                 <span v-if="item.paymentType">{{ item.paymentType }}</span>
                   <span v-else>
                    -
                 </span>
                </td>
                  <td>
                  <span>{{ item.status }} </span>
                </td>
              </tr>
              
            </tbody>
          </table>
        </div>

        <div class="">
          <nav
            aria-label="Page navigation"
            class="page navigation"
            v-if="last_page > 1"
          >
            <paginate
              :page-count="last_page"
              :click-handler="callbackPaginate"
              :prev-text="'<img src=' + arrow + ' width=' + 17 + '>'"
              :next-text="'<img src=' + arrow + ' width=' + 17 + '>'"
              :container-class="'pagination'"
              :page-class="'page-item'"
              :page-link-class="'page-link'"
              :prev-class="'page-item'"
              :prev-link-class="'page-link'"
              :next-class="'page-item'"
              :next-link-class="'page-link'"
              :page-range="5"
              :margin-pages="4"
              ref="paginate"
            >
            </paginate>
          </nav>
        </div>
      </div>
    </div>
  
  </div>
</template>

<script>
import { mapState } from "vuex";
import Paginate from "vuejs-paginate";
import debounce from "debounce";
import arrow from "@/assets/images/ic_arrow_square_hover.svg";
import Confirmation from "@/components/Confirmation";
import ConfirmationUpdate from "@/components/ConfirmationUpdate";
 import Multiselect from 'vue-multiselect'
export default {
  name: "report-list",
  components: {
    Multiselect,
    paginate: Paginate,
    confirmation: Confirmation,
    confirmationupdate: ConfirmationUpdate
  },
  computed: mapState(["user", "isAuthenticated"]),
  data() {
    return {
      profile: {},
      keyword: "",
      is_loading: false,
      data: [],
      selected_status: { id: "", name:"All" },
      status_options: [{ id: "", name:"All" },{id: "waiting", name: "Waiting"}, { id: "success",name: "Success"} , { id: "failed",name: "Failed"},{ id: "pending",name: "Pending"},{ id: "challenge",name: "Challenge"}, {id: "expired",name: "Expired"}],
      last_page: 0,
      current_page: 0,
      index_temp: "",
      arrow: arrow,
    };
  },
  mounted() {
    this.getAuthUser();
    this.getTransactionList();

  },
  methods: {
    getAuthUser() {
      if (this.isAuthenticated) {
        if (typeof this.user.id == typeof undefined || !this.user.id) {
          this.$http.get(process.env.API_URL + "user/profile").then(
            response => {
              this.profile = response.data.data;
            },
            response => {}
          );
        } else {
          this.profile = this.user;
        }
      }
    },
    callbackPaginate: function(pageNum) {
      this.getTransactionList(true, pageNum);
    },
    search: debounce(function() {
      this.getTransactionList(false);
    }, 250),
     getTransactionList(loading, page = null){  
        this.is_loading = loading;
        this.$http.get(process.env.API_URL + 'member/transaction-log', {params: { page: page, limit :10, status : this.selected_status === null ? '' : this.selected_status.id}}).then(response => {
             this.is_loading = false;
        var status = response.data.status
        if(status){
         this.data = response.data.data.data;
              this.last_page = response.data.data.last_page;
              this.current_page = response.data.data.current_page;
         }
      }, response => {})
    },
  }
};
</script>

<style lang="css" scoped>


.pointer {
  cursor: pointer;
  color: #00A6ED;
}
.class-filter{
  width: 750px;
  margin-bottom: 1.5rem;
}
.class-filter .form-check{
  display: inline-block;
  width: 30%;
  margin-bottom: .4rem;
}

@media (max-width: 1199px) {
  .board-report .col{
    width: 24%;
  }
}
@media (max-width: 991px) {
      
.class-filter{
  width: 100%;
 }
.class-filter .form-check{
  width: 38%;
 }
}
@media (max-width: 576px) {
.class-filter .form-check{
  width: 90%;
  }
}
</style>



