<template>
    <div class="terms">
        <div class="mb-4">
            <label class="page-header">PROOF OF HUSTLERS</label>
        </div>
        <label class="page-title">EDIT PROOF OF HUSTLERS</label>
        <div class="row">
            <div class="col-12 col-sm-11 col-md-10 col-lg-8">
                <div class="" v-if="is_load_data">
                    <i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i>
                </div>
                <form v-else class="mt-3" @submit.prevent="doUpdate()">
                   

                    <div class="form-group mb-45">
                        <label>TITLE 1<span class="red">*</span> </label>
                        <input type="text" class="form-control" v-model="form.title_1" required>
                    </div>

                    <div class="form-group mb-45">
                        <label>DESCRIPTION 1<span class="red">*</span> </label>
                        <textarea class="form-control" v-model="form.description_1" required> </textarea>
                    </div>

              

                    <div class="mt-5">
                        <button type="submit" class="btn btn-main small mr-2" :disabled="is_loading">
                            <span v-if="!is_loading">SAVE</span>
                            <span class="loading" v-else>
                                <i class="fa fa-spinner fa-pulse fa-fw"></i>
                            </span>
                        </button>
                        <router-link :to="{ name: 'terms_and_conditions', params: {id: id} }" class="btn btn-main inverse small">CANCEL</router-link>
                    </div>

                    <div class="loading">

                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
    import { Datetime } from 'vue-datetime'
import { mapState } from 'vuex'

    export default {
        name: 'edit_terms_and_conditions',
        props: ['id'],
        components: {
            'datetime': Datetime
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        data (){
            return {
                profile: {},
                data:{},
                is_member: false,
                is_can_edit: false,
                is_load_data: false,
                is_loading: false,
                form: {
                    category:'terms of service membership code',
                    title_1:'',
                    title_2:'-',
                    description_1:'',
                    description_2:'-'
                }
            }
        },
        mounted(){
            this.getPOH()
   
        },
        methods: {
            getPOH(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'term-and-condition/membership').then(response => {
                            this.data = response.data.data[0]
                            this.form.category = this.data.category
                            this.form.title_1 = this.data.title_1
                            this.form.description_1 = this.data.description_1
                         
                        }, response => {})
                    } else{
                        this.profile = this.user
                    }
                }
            },
    
       
            doUpdate(){
                this.is_loading = true
                this.$http.post(process.env.API_URL + 'term-and-condition/update/' + this.id, this.form).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        if(this.profile.id == this.data.id){
                            this.$parent.profile = response.data.data
                        }
                        this.$router.push({ name: 'terms_and_conditions', params: {id: this.id} })
                        this.$toastr('success', response.data.message)
                    }
                }, response => {})
            },
        }
    }
</script>

<style lang="css" scoped>
    .radio-group input[type=radio] {
        position: absolute;
        visibility: hidden;
        display: none;
    }

    .radio-group label {
        color: #ef3b29;
        display: inline-block;
        cursor: pointer;
        font-weight: bold;
        padding: 9px 20px;
        width: 100%;
        border: solid 1px #ef3b29;
    }
    .radio-group label::before{
        content: none;
    }
    .radio-group label::after{
        content: none;
    }

    .radio-group input[type=radio]:checked + label {
        color: #fff;
        background: #ef3b29;
        border: solid 1px #ef3b29;
    }

    .radio-group label + input[type=radio] + label{
        border: solid 1px #ef3b29;
    }

    .radio-group {
        overflow: hidden;
    }

    .terms textarea{
        min-height: 150px;
        display: block;
    }
</style>



