    import arrow from '@/assets/images/ic_arrow_square.svg'
    import Form from '@/components/Form'
    import Modal from '@/components/Modal'
    import Paginate from "vuejs-paginate";

    export default {
        name: 'schedule-list',
        props: ['date'],
        components: {
            Slick,
