              paginate: Paginate,
            'form-modal': Form,
            'datetime': Datetime,
            'booking-list': Modal
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        watch: {
            '$route': 'fetchData',
            show_form: function() {
            if(this.show_form){
                document.documentElement.style.overflow = 'hidden'
            return
            }
            document.documentElement.style.overflow = 'auto'
            },
            show_modal: function() {
            if(this.show_modal){
                document.documentElement.style.overflow = 'hidden'
            return
            }
            document.documentElement.style.overflow = 'auto'
            },
              show_form_duplicate: function() {
            if(this.show_form_duplicate){
                document.documentElement.style.overflow = 'hidden'
            return
            }
            document.documentElement.style.overflow = 'auto'
            },
            
        
        },
        data (){
            return {
                profile: {},
                keyword: '',
                is_loading: false,
                data: [],
                last_page: 0,
                current_page: 0,
                end_date: '',
                show_form: false,
                show_form_duplicate : false,
                selected_category: null,
                selected_category_id : null,
                category_options: [{id: "class", name: "Class"}, { id: "personal trainer",name: "Personal Trainer"} , { id: "wellness",name: "Wellness"},{ id: "recovery",name: "Recovery"}],
                filter_category: { id: "", name:"All" },
                filter_category_id : null,
                filter_location: { id: "", location_name:"All" },
                filter_location_id : null,
                category_options_filter: [{ id: "", name:"All" },{id: "class", name: "Class"}, { id: "personal trainer",name: "Personal Trainer"} , { id: "wellness",name: "Wellness"},{ id: "recovery",name: "Recovery"}],
                start_date: '',
                session_date: '',
                session_date_disabled : '',
                session_time : '',
                selected_class: null,
                selected_location : null,
                class_options: [],
                filter_location_options: [],
                location_options: [],
                has_error: false,
                save_data: false,
                form: {
                    start: '',
                    sportsClassID: '',
                    location: '',
                    teacher: '',
                    quota: '',
                },
                arrow: arrow,
                show_modal: false,
                item_temp: {},
                index_temp: '',

                slickOptions: {
                    infinite: false,
                    draggable: false,
                    initialSlide: 8,
                    slidesToShow: 7,
                    slidesToScroll: 7,
                    arrows: true,
                    prevArrow:"",
                    nextArrow:"",
                    autoplay: false,
                    autoplaySpeed: 3000,
                    responsive: [
                        {
                            breakpoint: 992,
                            settings: {
                                slidesToShow: 7,
                                slidesToScroll: 7,
                            }
                        },
                        {
                            breakpoint: 768,
                            settings: {
                                slidesToShow: 7,
                                slidesToScroll: 7
                            }
                        },
                        {
                            breakpoint: 576,
                            settings: {
                                slidesToShow: 7,
                                slidesToScroll: 7
                            }
                        },
                        {
                            breakpoint: 480,
                            settings: {
                                slidesToShow: 7,
                                slidesToScroll: 7
                            }
                        }
                    ]
                },
                days: [],
                is_view_booking_list: false,
                booking_list: [],
                selected_trainer: null,
                load_trainer: false,
                trainers: [],
            }
        },
        mounted(){
            this.getDefaultDate()
            this.getAuthUser()
            this.fetchData()
            this.getListClass()
            this.getTrainerList()
            this.getListLocation()
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                        }, response => {})
                    } else{
                        this.profile = this.user
                    }
                }
            },
             callbackPaginate: function(pageNum) {
                this.fetchData(pageNum);
            },
            getDefaultDate(){
                var moment = require('moment')
                var prevWeek = moment(this.date, 'DD-MM-YYYY').add(6, "days").format("YYYY-MM-DD");
                this.start_date = moment(this.date, 'DD-MM-YYYY').format('YYYY-MM-DD')
                this.end_date = prevWeek
            },
            fetchData(page = null){
                var moment = require('moment')
                var start_date = moment(this.start_date).format('YYYY-MM-DD')
                var end_date = moment(this.end_date).format('YYYY-MM-DD')
                this.is_loading = true
                this.filter_category_id = this.filter_category !== null  ?this.filter_category.id : ''
                 this.filter_location_id = this.filter_location !== null  ?this.filter_location.id : ''
                this.$http.get(process.env.API_URL + 'session', {params: {'start_date': start_date, end_date : end_date,  page: page, class_id : '', location_id : this.filter_location_id, teacher_id : '',category : this.filter_category_id }}).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.data = response.data.data.data
                        this.last_page = response.data.data.last_page;
                        this.current_page = response.data.data.current_page;
                    }
                }, response => {})
            },
            getListClass(query = null){
                this.$http.get(process.env.API_URL + 'sports-class/sportsClassList',{params: {'keyword': query}}).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.class_options = response.data.data.data
                    }
                }, response => {})
            },
            getSelectCategory(){
                this.selected_category_id = this.selected_category.id
                 if(this.selected_category !== null && this.selected_category.id === 'class'){
                    this.selected_class = null
                    this.getListClass()
                    this.getTrainerClassList()
                }else if(this.selected_category !== null && this.selected_category.id === 'recovery'){
               this.selected_class = null
                    this.getRecoveryList()
                
                }else if(this.selected_category !== null && this.selected_category.id === 'wellness'){
                 this.selected_class = null
                    this.getWellnessList()
            
                }else if(this.selected_category !== null && this.selected_category.id === 'personal trainer') {
                 this.selected_class = null

                    this.getTrainerList()
                }
             
            },
            getListLocation(query = null){
                this.$http.get(process.env.API_URL + 'location/all',{params: {'keyword': query}}).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.location_options = response.data.data
                        var response = response.data.data
                        var array1 = [{id:'', location_name: 'All'}]
                        this.filter_location_options = [...array1,...response]
                        
                    }
                }, response => {})
            },
             getTrainerList(){
                this.load_trainer = true
                this.$http.get(process.env.API_URL + 'teacher/teacher-class', {params: {'category' : 'personal trainer' }}).then(response => {
                    this.load_trainer = false
                    var status = response.data.status
                    if(status){
                        this.trainers = response.data.data
                    }
                }, response => {})
            },
       
            getTrainerClassList(){
                this.load_trainer = true
                this.$http.get(process.env.API_URL + 'teacher/teacher-class', {params: {'category' : 'class' }}).then(response => {
                    this.load_trainer = false
                    var status = response.data.status
                    if(status){
                        this.trainers = response.data.data
                    }
                }, response => {})
            },
            getRecoveryList(query = null){
                this.$http.get(process.env.API_URL + 'recovery/recoveryList', {params: {'keyword': query}}).then(response => {
                var status = response.data.status
                if(status){
              
                    this.class_options = response.data.data
                 }
                }, response => {})
                },
            getWellnessList(query = null){
                this.$http.get(process.env.API_URL + 'wellness/wellnessList', {params: {'keyword': query}}).then(response => {
                var status = response.data.status
                if(status){
             
                    this.class_options = response.data.data
                }
                }, response => {})
            },
            addTrainer(newTeacher){
                this.selected_trainer = {
                    id: newTeacher,
                    firstName: newTeacher.split(' ')[0],
                    lastName: newTeacher.split(' ')[1]
                }
            },
            trainerFormatName({ firstName, lastName }){
                return `${firstName} ${lastName}`
            },

            doCreate(){
                  var moment = require('moment')
                if(this.selected_category == null){
                    this.$toastr('error', 'Category required')
                    return
                }
                if(this.selected_class == null && this.selected_category_id !== 'personal trainer'){
                    this.$toastr('error', 'Class required')
                    return
                }
                if(this.selected_location == null && this.selected_category_id !== 'personal trainer'){
                    this.$toastr('error', 'Location required')
                    return
                }

                 var moment = require('moment')

                if(this.show_form_duplicate){
                
                    var session_time_formated = moment(this.session_time).format('HH:mm:ss')
                    var session_date_formated = moment(this.session_date_disabled).format('YYYY-MM-DD')
                    this.session_date = session_date_formated + " " + session_time_formated

                }

                var date = moment(this.session_date).format('DD-MM-YYYY')
                this.save_data = true
                this.form.sportsClassID = this.selected_class ? this.selected_class.id : ''
                this.form.location = this.selected_location ? this.selected_location.id : ''
                this.form.start = moment(this.session_date).format('YYYY-MM-DD HH:mm:ss')
                this.form.teacher = this.selected_trainer ? this.selected_trainer.id : ''
                
                var formData = new FormData();
                if(this.selected_category_id === 'class' ){
                    formData.append('teacherID', this.form.teacher)
                    formData.append('start', this.form.start)
                    formData.append('location', this.form.location)
                    formData.append('sportsClassID', this.form.sportsClassID)
                    formData.append('quota', this.form.quota)
                  
                     this.$http.post(process.env.API_URL + 'session', formData).then(response => {
                    var status = response.data.status
                    if(status){
                        this.selected_class = null
                        this.selected_trainer = null
                        this.selected_location = null
                        this.form.sportsClassID = ''
                        this.selected_category_id = null
                        this.selected_category = null
                        this.form.quota = ''
                        this.form.location = ''
                        this.form.start = ''
                        this.session_date = ''
                        this.save_data = false
                        this.show_form = false
                        this.show_form_duplicate = false
                        this.$toastr('success', response.data.message)

                        if(date == this.date){
                            this.fetchData()
                        }
                        else {
                            this.$router.push({ name: 'schedule' })
                        }
                    } else {
                        this.save_data = false
                    }
                }, response => {})
                }else if(this.selected_category_id === 'personal trainer'){
                    formData.append('start', this.form.start)
                    // formData.append('location', this.form.location)
                    formData.append('teacherID', this.form.teacher)
                      formData.append('quota', this.form.quota)
                     this.$http.post(process.env.API_URL + 'session/teacher', formData).then(response => {
                    var status = response.data.status
                    if(status){
                        this.selected_class = null
                        this.selected_trainer = null
                        this.selected_location = null
                        this.form.sportsClassID = ''
                        this.selected_category = null
                        this.selected_category_id = null
                        this.form.location = ''
                        this.form.quota = ''
                        this.form.start = ''
                        this.session_date = ''
                        this.save_data = false
                        this.show_form = false
                        this.show_form_duplicate = false
                        this.$toastr('success', response.data.message)

                        if(date == this.date){
                            this.fetchData()
                        } else {
                            this.$router.push({ name: 'schedule' })
                        }
                    } else {
                        this.save_data = false
                    }
                }, response => {})
                  
                }else if(this.selected_category_id === 'wellness') {
                    formData.append('sportsClassID', this.form.sportsClassID)
                    formData.append('start', this.form.start)
                    formData.append('location', this.form.location)
                     formData.append('teacherID', '')
                      formData.append('quota', this.form.quota)
                       this.$http.post(process.env.API_URL + 'session/wellness', formData).then(response => {
                    var status = response.data.status
                    if(status){
                        this.selected_class = null
                        this.selected_trainer = null
                        this.selected_location = null
                        this.form.sportsClassID = ''
                         this.selected_category = null
                        this.selected_category_id = null
                        this.form.location = ''
                        this.form.quota = ''
                        this.form.start = ''
                        this.session_date = ''
                        this.save_data = false
                        this.show_form = false
                        this.show_form_duplicate = false
                        this.$toastr('success', response.data.message)

                        if(date == this.date){
                            this.fetchData()
                        } else {
                            this.$router.push({ name: 'schedule' })
                        }
                    } else {
                        this.save_data = false
                    }
                }, response => {})
                }else if(this.selected_category_id === 'recovery') {
                    formData.append('sportsClassID', this.form.sportsClassID)
                    formData.append('start', this.form.start)
                    formData.append('location', this.form.location)
                    formData.append('teacherID', '')
                    formData.append('quota', this.form.quota)
                    this.$http.post(process.env.API_URL + 'session/recovery', formData).then(response => {
                    var status = response.data.status
                    if(status){
                        this.selected_class = null
                        this.selected_trainer = null
                        this.selected_location = null
                        this.form.sportsClassID = ''
                         this.selected_category = null
                        this.selected_category_id = null
                        this.form.quota = ''
                        this.form.location = ''
                        this.form.start = ''
                        this.session_date = ''
                        this.save_data = false
                        this.show_form = false
                          this.show_form_duplicate = false
                        this.$toastr('success', response.data.message)

                        if(date == this.date){
                            this.fetchData()
                        } else {
                            this.$router.push({ name: 'schedule' })
                        }
                    } else {
                        this.save_data = false
                    }
                }, response => {})
                }
                
               
            },
          
            doCloseForm(){
             
                this.show_form = false
                 this.selected_category = null
                this.selected_category_id = null
                this.selected_class = null
                this.selected_trainer = null
                this.form.sportsClassID = ''
                this.form.quota = ''
                this.form.start = ''
                this.session_date = ''
                this.save_data = false
                this.show_form_duplicate = false
            },
            showBookingList(item, index){
                this.show_modal = true
                this.item_temp = item
                this.index_temp = index

                this.is_view_booking_list = true
                this.$http.get(process.env.API_URL + 'session/booking-list/' + item.id).then(response => {
                    this.is_view_booking_list = false
                    var status = response.data.status
                    if(status){
                        this.booking_list = response.data.data
                    }
                }, response => {})
                document.body.style.overflowY = 'hidden';
            },
            showDuplicate(item, index) {
                this.show_form_duplicate = true;
                 var moment = require('moment')
           
                this.selected_category = {id : item.category, name : item.category.toUpperCase()}
                this.selected_category_id = item.category
                    if(this.selected_category !== null && this.selected_category.id === 'class'){
                       
                        this.getListClass()
                        this.getTrainerClassList()
                         this.selected_class = {id : item.sportsClassID, name: item.sportsClass.name}
                        this.selected_location = {id : item.location.id, location_name: item.location.location_name}
                        this.session_date_disabled = moment(item.start).format('YYYY-MM-DD')
                         
                    }else if(this.selected_category !== null && this.selected_category.id === 'recovery'){
                        
                        this.getRecoveryList()
                          this.selected_class = {id : item.sportsClassID, name: item.sportsClass.name}
                        this.selected_location = {id : item.location.id, location_name: item.location.location_name}
                        this.session_date_disabled = moment(item.start).format('YYYY-MM-DD')
                    
                    }else if(this.selected_category !== null && this.selected_category.id === 'wellness'){
                       
                        this.getWellnessList()
                          this.selected_class = {id : item.sportsClassID, name: item.sportsClass.name}
                        this.selected_location = {id : item.location.id, location_name: item.location.location_name}
                        this.session_date_disabled = moment(item.start).format('YYYY-MM-DD')
                
                    }else if(this.selected_category !== null && this.selected_category.id === 'personal trainer') {
                        this.selected_class = null

                        this.getTrainerList()
                        this.session_date_disabled = moment(item.start).format('YYYY-MM-DD')
                    }
            },
            doCloseModal(){
                this.show_modal = false
                this.item_temp = {}
                this.index_temp = ''
            },
        }
    }
</script>

<style lang="css" scoped>

    .calendar{
        width: 100%;
        margin-top: 20px;
        margin-bottom: 35px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .calendar > div{
        display: inline-block;
    }
    .calendar .prev-week,
    .calendar .next-week{
        width: 4%;
        text-align: center;
    }
    .calendar .week-days{
        width: 90%;
        overflow: hidden;
    }
    .calendar .prev-week img{
        transform: rotate(-180deg);
    }

    .calendar /deep/ .slick-slide{
        text-align: center;
        padding-left: 2px;
        padding-right: 4px;
    }
    .calendar /deep/ .slick-slide a{
        color: #ef3b29;
        padding-top: 5px;
        padding-bottom: 5px;
    }
    .calendar /deep/ .slick-slide a.active{
        background-color: #ef3b29;
        color: #fff;
        border-radius: 25px;
        outline: none;
    }

    [booking-list] /deep/ .modal-wrapper{
        width: 95%;
      
    }


    @media (max-width: 1199px) {
        .calendar{
            margin-left: -2px;
        }
        .calendar img{
            width: 25px;
        }
        .calendar .prev-week,
        .calendar .next-week{
            width: 25px;
        }
        .calendar .next-week{
            margin-right: -3px;
        }
        .calendar .week-days{
            width: 100%;
        }
    }
    @media (max-width: 991px) {

    }
    @media (max-width: 767px) {
        .calendar{
            margin-left: -2px;
        }
        .calendar /deep/ .slick-slide{
            padding-right: 7px;
        }
    }
    @media (max-width: 575px) {
        .calendar .prev-week{
            margin-left: 0px;
        }
        .calendar .next-week{
            margin-right: -4px;
        }
        .calendar .slick-slide a.active{
            border-radius: 15px;
            width: 45px !important;
        }
    }
    @media (max-width: 480px) {
        .calendar{
            margin-left: -1px;
        }
        .calendar img{
            width: 20px;
        }
        .calendar .prev-week,
        .calendar .next-week{
            width: 20px;
        }
        .calendar .prev-week{
            margin-left: -10px;
        }
        .calendar .next-week{
            margin-right: -10px;
        }
        .calendar .slick-slide a.active{
            border-radius: 15px;
            width: 30px !important;
        }
    }
    @media (max-width: 375px) {
        .calendar .slick-slide a.active{
            border-radius: 15px;
            width: 100% !important;
        }
    }
</style>



