<template>
    <div class="">
        <div>
            <label class="page-header">CREDIT PRICE GUIDE</label>
            <label class="breadcrumb">Credit Price Guide List</label>
        </div>
    <div class="mb-2">
            <label class="page-header">SUBCONTENT</label>
        </div>

        <div class="table-responsive mb-4">
                    <table class="table">
                        <thead>
                            <tr>
       
                                <th>TITLE</th>
                                <th>DESCRIPTION</th>
                           
                                <th style="width: 24%;">ACTION</th>
                            </tr>
                        </thead>
                        <tbody v-if="is_loading">
                            <tr>
                                <td colspan="8" class="text-center">
                                    <span ><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                </td>
                            </tr>
                        </tbody>
                        <tbody  v-else>
                          
                               <tr class="text-center" v-if="is_loading">
                                <td colspan="8">
                                    <span><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                </td>
                            </tr>
                            <tr  v-else>
                           
                                <td>{{datasubcontent.title}}</td>
                                <td>{{datasubcontent.description}}</td>
                           
                              
                                <td>
                                    <router-link :to="{ name: 'edit_subcontent', params: {id: datasubcontent.id} }" class="btn btn-main inverse small mt-1 mb-1">EDIT</router-link>
                                  
                                </td>
                            </tr>
                        </tbody>
                    </table>
         </div>

        <div class="row justify-content-between align-items-center mb-2">
            <div class="col-12 col-sm-6 col-md-5 col-lg-4 col-xl-3 mb-3">
                <router-link :to="{ name: 'create_credit_price', params: {} }" class="btn btn-main inverse small">+ CREDIT PRICE GUIDE</router-link>
            </div>
            <div class="col-12 col-sm-6 col-md-5 col-lg-3 mb-3">
                <form class="" @submit.prevent="search()">
                    <div class="form-group search">
                        <input type="text" class="form-control small search" placeholder="search" v-model="keyword" @keyup="search()">
                        <span class="icon-search">
                            <img src="~@/assets/images/ic_search.svg" alt="">
                        </span>
                    </div>
                </form>
            </div>
        </div>

        <div class="row">
            <div class="col-12">
                <div class="table-responsive">
                    <table class="table">
                        <thead>
                            <tr>
                                <th>TITLE</th>
                                <th>PRICE</th>
                                <th>CREDIT START</th>
                                <th>CREDIT END</th>
                                <th>EXPIRY</th>
                                <th style="width: 24%;">ACTION</th>
                            </tr>
                        </thead>
                        <tbody v-if="is_loading">
                            <tr>
                                <td colspan="7" class="text-center">
                                    <span ><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                </td>
                            </tr>
                        </tbody>
                        <tbody v-else>
                            <tr class="text-center" v-if="data.length <= 0">
                                <td colspan="7">
                                    <span >No matching records found</span>
                                </td>
                            </tr>
                            <tr v-else v-for="(item, index) in data" :key="index" >
                                <td>
                                    <span>{{ item.title }}</span>
                                </td>
                                <td>
                                    <span>{{ item.price | currencyFormat }}</span>
                                </td>
                                <td>
                                    <span>{{ item.credit_start | numberFormat }}</span>
                                </td>
                                <td>
                                    <span>{{ item.credit_end | numberFormat }}</span>
                                </td>
                                <td>
                                    <span>{{ item.expiry | numberFormat }}</span>
                                </td>
                                <td>
                                    <router-link :to="{ name: 'edit_credit_price', params: {id: item.id} }" class="btn btn-main inverse small mt-1 mb-1">EDIT</router-link>
                                    <button type="button" class="btn btn-main inverse small mt-1 mb-1" @click="showDeleteConfirmation(item, index)">DELETE</button>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>

                <div class="">
                    <nav aria-label="Page navigation" class="page navigation" v-if="last_page > 1">
                        <paginate
                            :page-count="last_page"
                            :click-handler="callbackPaginate"
                            :prev-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                            :next-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                            :container-class="'pagination'"
                            :page-class="'page-item'"
                            :page-link-class="'page-link'"
                            :prev-class="'page-item'"
                            :prev-link-class="'page-link'"
                            :next-class="'page-item'"
                            :next-link-class="'page-link'"
                            :page-range="5"
                            :margin-pages="4"
                            ref="paginate">
                        </paginate>
                    </nav>
                </div>
            </div>
        </div>
        <confirmation v-if="delete_confirmation" @close="delete_confirmation = false" @confirm="deleteCreditPriceGuide()">
            <div slot="header">DELETE CREDIT PRICE GUIDE</div>
            <div slot="body">
                are you sure you want to delete this credit price guide? this action can not be reverted
            </div>
        </confirmation>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import Paginate from 'vuejs-paginate'
    import debounce from 'debounce'
    import arrow from '@/assets/images/ic_arrow_square_hover.svg'
    import Confirmation from '@/components/Confirmation'

    export default {
        name: 'credit-price-list',
        components: {
            'paginate': Paginate,
            'confirmation': Confirmation
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
         watch: {
       
            delete_confirmation: function() {
            if(this.delete_confirmation){
                document.documentElement.style.overflow = 'hidden'
            return
            }
            document.documentElement.style.overflow = 'auto'
            }
        },
        data (){
            return {
                profile: {},
                keyword: '',
                sort_column: 'email', // * fullName * role * email * phoneNumber *
                sort_type: 'asc',
                is_loading: false,
                data: [],
                datasubcontent: [],
                last_page: 0,
                current_page: 0,
                delete_confirmation: false,
                data_temp: {},
                index_temp: '',
                arrow: arrow,
            }
        },
        mounted(){
            this.getAuthUser()
            this.getListUser(true)
            this.getListsubcontent(true)
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                        }, response => {})
                    } else{
                        this.profile = this.user
                    }
                }
            },
            callbackPaginate: function(pageNum) {
                this.getListUser(true, pageNum, this.sort_column, this.sort_type)
            },
            search: debounce(function(){
                this.getListUser(false)
            },250),
            doSortTable(value){
                if(value == this.sort_column){
                    if(this.sort_type == 'asc'){
                        this.sort_type = 'desc'
                    } else {
                        this.sort_type = 'asc'
                    }
                } else {
                    this.sort_type = 'asc'
                }
                this.sort_column = value
                this.getListUser(true, null, this.sort_column, this.sort_type)
            },
              getListsubcontent(loading, page = null, sortColumn = null, sortType = null){
                this.is_loading = loading

                this.$http.get(process.env.API_URL + 'subcontent/2', {params: {'keyword': this.keyword, 'page': page, 'sortColumn': sortColumn, 'sortType': sortType}}).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.datasubcontent = response.data.data
                 
                    }
                }, response => {})
            },
            getListUser(loading, page = null, sortColumn = null, sortType = null){
                this.is_loading = loading

                this.$http.get(process.env.API_URL + 'credit-price', {params: {'keyword': this.keyword, 'page': page, 'sortColumn': sortColumn, 'sortType': sortType}}).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.data = response.data.data.data
                        this.last_page = response.data.data.last_page
                        this.current_page = response.data.data.current_page
                    }
                }, response => {})
            },
            showDeleteConfirmation(data, index){
                this.data_temp = data
                this.index_temp = index
                this.delete_confirmation = true
            },
            deleteCreditPriceGuide() {
            
              this.$http.post(process.env.API_URL + 'credit-price/delete/' + this.data_temp.id).then(response => {
                    this.delete_confirmation = false
                    document.documentElement.style.overflow = 'auto'
                    var status = response.data.status
                    if(status){
                        this.data.splice(this.index_temp, 1)
                        this.$toastr('success', response.data.message)
                    } else {
                        this.$toastr('error', response.data.message)
                    }
                }, response => {})
            }
        }
    }
</script>

<style lang="css" scoped>
    label.title{
        font-size: 14px;
        color: #ef3b29;
        font-family: 'gothambold';
    }
    p{
        color: #000;
    }

    @media (max-width: 480px) {
        [action-button]{
            display: block;
        }
    }
</style>



