<template>
    <div class="">
        <div class="mb-4">
            <label class="page-header">CREDIT PRICE GUIDE</label>
            <label class="breadcrumb">Credit Price Guide List/Create Credit Price Guide</label>
        </div>
        <label class="page-title">CREATE NEW CREDIT PRICE GUIDE</label>
        <div class="row">
            <div class="col-12 col-sm-11 col-md-10 col-lg-8">
                <form class="mt-3" @submit.prevent="doCreate()">
                    <div class="form-group mb-45">
                        <label>TITLE <span class="red">*</span> </label>
                        <input type="text" class="form-control" v-model="form.title" required>
                    </div>
                    <div class="form-group mb-5">
                        <label>PRICE  <span class="red">*</span> </label>
                        <div class="input-group front">
                            <span class="front">Rp</span>
                            <input type="text" class="form-control" v-model="form.price" @keyup="formatPrice(form.price)" required>
                        </div>
                    </div>
                    <div class="form-group mb-5">
                        <label>CREDIT START <span class="red">*</span> </label>
                        <input type="number" class="form-control" v-model="form.credit_start" min="0" required />
                    </div>
                    <div style="display:flex; gap: 1rem;">
                      <div class="form-group mb-5 flex-auto">
                          <label>CREDIT END <span v-if="!form.is_up_to" class="red">*</span> </label>
                          <div>
                            <input :disabled="form.is_up_to" type="number" class="form-control" v-model="form.credit_end" min="0" required />
                          </div>
                      </div>
                      <div class="form-group form-check mb-45 flex flex-none">
                        <div class="checkbox">
                            <input type="checkbox" id="isupto" v-model="form.is_up_to" @change="changeIsUpto"/>
                            <label class="red font-bold" for="isupto">
                                <div class=""><i class="fa fa-check"></i></div> Up To
                            </label>
                        </div>
                      </div>
                    </div>
                    <div class="form-group mb-45">
                        <label class="mb-2">EXPIRY <span class="red">*</span></label>
                        <div class="row">
                            <div class="col-10 col-sm-8 col-md-5 mb-2">
                                <div class="input-group back">
                                    <input type="number" class="form-control" v-model="form.expiry" min="1" required>
                                    <span class="back">days</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="mt-5">
                        <button type="submit" class="btn btn-main small mr-2" :disabled="is_loading">
                            <span v-if="!is_loading">CREATE</span>
                            <span class="loading" v-else>
                                <i class="fa fa-spinner fa-pulse fa-fw"></i>
                            </span>
                        </button>
                        <router-link :to="{ name: 'credit_price', params: {} }" class="btn btn-main inverse small">CANCEL</router-link>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import { Datetime } from 'vue-datetime'

    export default {
        name: 'create-credit-price',
        components: {
            'datetime': Datetime
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        data (){
            return {
                profile: {},
                is_loading: false,
                form: {
                    title: '',
                    price: '',
                    credit_start: '',
                    credit_end: '',
                    expiry: '',
                    is_up_to: false,
                }
            }
        },
        mounted(){
            this.getAuthUser()
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                        }, response => {})
                    } else{
                        this.profile = this.user
                    }
                }
            },
            doCreate(){
                this.is_loading = true
                const payload = {
                  title: this.form.title,
                  price: parseInt(this.form.price.toString().replace(/\./g,'')),
                  credit_start: parseInt(this.form.credit_start),
                  credit_end: parseInt(this.form.credit_end),
                  expiry: parseInt(this.form.expiry),
                  is_up_to: this.form.is_up_to
                }
                this.$http.post(process.env.API_URL + 'credit-price', payload).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.$router.push({ name: 'credit_price', params: {} })
                        this.$toastr('success', response.data.message)
                    }
                }, response => {})
            },
            formatPrice(value){
                this.form.price = this.formatNumber(value)
            },
            formatNumber(value){
                let temp = value.toString().replace(/\./g,'')
                let result = temp.replace(/[^0-9,]/g, "").replace(/(\d)(?=(\d\d\d)+(?!\d))/g, "$1.")
                return result
            },
            changeIsUpto() {
              if (this.form.is_up_to) {
                this.form.credit_end = '999999999'
              } else {
                this.form.credit_end = ''
              }
            }
        }
    }
</script>

<style lang="css" scoped>
    .flex-auto {
      flex: 1 1 auto;
    }
    .flex-none {
      flex: none
    }
    @media (max-width: 375px) {
        .btn.small{
            min-width: 100px
        }
    }
</style>



