<template>
    <div class="">
        <div class="mb-4">
            <label class="page-header">BANNER</label>
            <label class="breadcrumb">Banner List/Edit Class</label>
        </div>
        <label class="page-title">EDIT Banner</label>
        <div class="row">
            <div class="col-12 col-sm-11 col-md-10 col-lg-8">
                <div class="" v-if="is_load_data">
                    <i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i>
                </div>
                <form v-else class="mt-3" @submit.prevent="doUpdate()">
                    <div class="form-group mb-45">
                        <label>BANNER IMAGE <span class="red">* (347 x 200px)</span> </label>
                        <div class="upload-wrapper continue mt-2">
                            <button type="button" class="btn btn-upload btn-main small">UPLOAD IMAGE</button>
                            <span class="ellipsis" v-if="file_name">{{file_name}}</span>
                            <span v-else class="ellipsis">No Choosen File</span>
                            <input type="file" value="" @change="onChangeFile" class="form-control hidden-input" accept="image/*">
                        </div>
                    </div>

                    <div class="form-group mb-45">
                        <label>CLASS TITLE</label>
                        <input type="text" class="form-control" v-model="form.name">
                    </div>

                    <div class="form-group mb-45">
                        <label>DESCRIPTION <span class="red">*</span> </label>
                        <input type="text" class="form-control" v-model="form.description" required>
                    </div>

                    <div class="form-group mb-45">
                        <label>LINK <span class="red">*</span> </label>
                        <input type="text" class="form-control" v-model="form.link" required>
                    </div>

                    <div class="form-group mb-5">
                        <label class="mb-2">PRIORITY <span class="red">*</span></label>
                        <div class="row">
                            <div class="col-6 col-sm-4 mb-2">
                                <multiselect v-model="form.priority" :options="priority_options" :searchable="false" :close-on-select="true" :show-labels="false" placeholder="Choose Order"></multiselect>
                            </div>
                        </div>
                    </div>

                    <div class="">
                        <button type="submit" class="btn btn-main small mr-2" :disabled="is_loading">
                            <span v-if="!is_loading">SAVE</span>
                            <span class="loading" v-else>
                                <i class="fa fa-spinner fa-pulse fa-fw"></i>
                            </span>
                        </button>
                        <router-link :to="{ name: 'banner', params: {id: id} }" class="btn btn-main inverse small">CANCEL</router-link>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import Multiselect from 'vue-multiselect'

    export default {
        name: '',
        props: ['id'],
        components: {
            Multiselect
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        data (){
            return {
                IMAGE_URL: process.env.IMAGE_URL,
                is_load_data: false,
                is_loading: false,
                file: null,
                file_name: '',
                priority_options: [],
                form: {
                    image: '',
                    name: '',
                    priority: '',
                    description: '',
                    link:''
                },
            }
        },
        mounted(){
            this.getAuthUser()
            this.getDetailBanner()
            this.getListBanner()
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                            this.checkAuthority()
                        }, response => {})
                    } else{
                        this.profile = this.user
                        this.checkAuthority()
                    }
                }
            },
            getListBanner(){
                this.$http.get(process.env.API_URL + 'banner').then(response => {
                    var status = response.data.status
                    if(status){
                        var data = response.data.data
                        for(var i = 0; i < data.length; i++){
                            this.priority_options.push(i + 1)
                        }
                    }
                }, response => {})
            },
            checkAuthority(){
                if(this.profile.isOperator){
                    this.$toastr('error', "You don't have authorization to access this page!")
                    this.$router.push({ name: 'class', params: {} })
                }
            },
            getDetailBanner(){
                this.is_load_data = true
                this.$http.get(process.env.API_URL + 'banner/' + this.id).then(response => {
                    this.is_load_data = false
                    if(response.status == 404){
                        this.$toastr('error', response.statusText)
                        this.$router.push({ name: 'banner', params: {} })
                    } else if(response.status == 200){
                        var status = response.data.status
                        if(status){
                            this.form.name = response.data.data.title
                            this.form.description = response.data.data.description
                            this.form.priority = response.data.data.priority
                            this.file_name = response.data.data.image
                            this.form.link = response.data.data.link
                        }
                    }
                }, response => {})
            },
            onChangeFile(e) {
                var files = e.target.files || e.dataTransfer.files;
                if (!files.length)
                    return;
                this.file = files[0];
                this.file_name = files[0].name
            },
            doUpdate(){
                this.is_loading = true

                var formData = new FormData();
                if(this.file !== null){
                    formData.append('image', this.file)
                }
                formData.append('title', this.form.name)
                formData.append('priority', this.form.priority)
                formData.append('description', this.form.description)
                formData.append('link', this.form.link)

                this.$http.post(process.env.API_URL + 'banner/update/' + this.id, formData).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.$router.push({ name: 'banner', params: {} })
                        this.$toastr('success', response.data.message)
                    }
                }, response => {})
            }
        }
    }
</script>

<style lang="css" scoped>
    .input-group input{
        padding-right: 40px;
    }
    .input-group span{
        position: absolute;
        right: 5px;
        top: 10px;
        color: #ef3b29;
    }
</style>



